// ----------------------------------------------------------------------------
// Pagination
// ----------------------------------------------------------------------------
//
// List of paginated page numbers
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Default
// -------------------------------------
.page-section--pagination {
    margin: 0;
}

.pagination {
    list-style: none;
    text-align: center;
    padding-top: gap(6);
    display: flex;
    align-items: center;
    justify-content: center;
}

.pagination__item {
    display: inline-block;
    text-decoration: none;
    border: 0;
    color: $secondary-color;
    display: block;
    //padding: gap(1);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    border-radius: 25px;

    @include bp(laptop) {
        width: 40px;
        height: 40px;
    }

    .icon {
        display: block;
        width: 15px;
        height: 15px;
    }

    & + .pagination__item {
        margin-left: gap(.5);
    }
}

.pagination__pages {
    margin: 0 gap(2);

    .pagination__page {
        @include font-size(16);
        @include line-height(24);
        color: $black;
        text-decoration: none;
        font-weight: 400;

        &:not(:last-child) {
            margin-right: gap(2);
        }

        &.pagination__page--current {
            font-weight: 700;
        }
    }
}

.pagination__item--disabled {
    display: none;
}


