// ----------------------------------------------------------------------------
// FAQs
// ----------------------------------------------------------------------------
//
// FAQs sections
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// FAQs, Block Styles
// -------------------------------------

.faq-block {
    @include container;
    max-width: 1064px;
}

.faq-block__h3 {
    margin-top: 0;
    margin-bottom: gap(2);


    @include bp(laptop) {
        text-align: center;
    }

    & + .accordion {
        margin-top: gap(3);

        @include bp(laptop) {
            margin-top: gap(5);
        }
    }
}

.faq-block__description {
    margin-bottom: gap(3);

    @include bp(laptop) {
        margin-bottom: gap(5);
        text-align: center;
    }
}

.faq-block__footer {
    padding-top: gap(3);
    padding-bottom: gap(2);

    @include bp(laptop) {
        text-align: center;
        padding-top: gap(5);
    }
}


// -------------------------------------
// FAQs Section
// -------------------------------------

.page-section--faq {
    background-color: $background-color;
    padding: gap(8.5) 0;
    padding-bottom: gap(6.5);

    @include bp(laptop) {
        padding: gap(6.5) 0;
        padding-bottom: gap(4.5);
    }
}

