// ----------------------------------------------------------------------------
// Header
// ----------------------------------------------------------------------------
//
// Basic site announcement bar.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$announcement-bar-background: $primary-color !default;
$announcement-bar-text-color: $white !default;

// -------------------------------------
// Announcement Bar
// -------------------------------------
.announcement-bar {
    background-color: $announcement-bar-background;
    color: $announcement-bar-text-color;
    padding: 10px 0;
    text-align: center;
    position: relative;
    z-index: $z-index--header;
    display: block;

    &.announcement-bar--hidden {
        display: none;
    }

    .container {
        position: relative;
    }

    .announcement-bar__link,
    .announcement-bar__text {
        @include font-default;
        @include font-size(16);
        @include line-height(26);
        color: $announcement-bar-text-color;
        text-decoration: none;
        margin-bottom: 0;

        @include bp(tablet) {
            margin: 0 35px;
        }
    }

    .announcement-bar__close {
        @include font-default;
        @include font-size(16);
        @include line-height(26);
        background-color: transparent;
        color: $announcement-bar-text-color;
        text-transform: none;
        font-weight: normal;
        text-decoration: underline;
        position: absolute;
        top: 0;
        right: 0px;
        padding: 3px 0;
    }
}
