// ----------------------------------------------------------------------------
// Vision Block
// ----------------------------------------------------------------------------
//
// A section with subtitle, title, and textarea
//
// ----------------------------------------------------------------------------



// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// ----------------------------------------
// Rich Text Editor Styles
// ----------------------------------------
.rte {
    margin: 0;

    img {
        height: auto;
    }

    table {
        table-layout: fixed;
    }

    ul,
    ol {
        margin: 0 0 0 gap(1);
    }

    strong {
        font-weight: bold;
    }
}

// Allow table to scroll for tables in the RTE since we don't know
// how many columns they will contain. Class added by JS.
.rte-table {
    overflow: auto;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
}

// This class is wrapped around YouTube/Vimeo embeds in the RTE
// to make them responsive and prevent layout breaking
.rte__video-wrapper {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    height: 0;
    height: auto;
    padding-bottom: 56.25%;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

// This class is wrapped around tables in the RTE
// to prevent layout breaking with a scrollable parent
.rte__table-wrapper {
    overflow: auto;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
}

// Custom class for the Vision Block
.page-section--vision-block{
    text-align: left;

    @include bp(laptop) {
        text-align: center;
    }

    .subtitle{
        display: block;
    }

    .page-section__title{
        margin: 10px 0;
        @include responsive-fontsize(32, 40, 52, 64);
    }

    .page-section__content {
        @include font-header;
        @include responsive-fontsize(16, 24, 24, 32);
        margin-top: 20px;
        color: $black;
    }

    .container{
        max-width: 1100px;
        width: 100%;
    }
}
