// ----------------------------------------------------------------------------
// Rich Text
// ----------------------------------------------------------------------------
//
// A section with a rich text editor and section title
//
// ----------------------------------------------------------------------------



// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// ----------------------------------------
// Rich Text Editor Styles
// ----------------------------------------
.rte {
    margin: 0;

    img {
        height: auto;
    }

    table {
        table-layout: fixed;
    }

    ul,
    ol {
        margin: 0 0 0 gap(1);
    }

    strong {
        font-weight: bold;
    }
}

// Allow table to scroll for tables in the RTE since we don't know
// how many columns they will contain. Class added by JS.
.rte-table {
    overflow: auto;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
}

// This class is wrapped around YouTube/Vimeo embeds in the RTE
// to make them responsive and prevent layout breaking
.rte__video-wrapper {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    height: 0;
    height: auto;
    padding-bottom: 56.25%;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

// This class is wrapped around tables in the RTE
// to prevent layout breaking with a scrollable parent
.rte__table-wrapper {
    overflow: auto;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
}

.page-section--wysiwyg{
    .page-section__title{
        @include responsive-fontsize(32, 40, 30, 40);
        margin-top: 0;
        margin-bottom: 23px;
    }

    .container--wysiwyg{
        max-width: 955px;
        margin-left: 0;
        margin-right: auto;
    }
}
