// ------------------------------------------------------------------------------
// Featured Collection Block
// ------------------------------------------------------------------------------
//
// This is a section that displays featured collection items and optional button
//
// ------------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Carousel -- Featured Collection
// -------------------------------------
$carousel-arrow-background: $white;
$carousel-arrow-border-radius: 65px !default;
$carousel-arrow-color: $secondary-color;
$carousel-arrow-padding: 0;

.carousel--featured-collection {
    .swiper-wrapper {
        position: relative;
        padding: 0;
        list-style-type: none;
    }

    .swiper-arrow {
        background: $carousel-arrow-background;
        border: 0;
        border-radius: $carousel-arrow-border-radius;
        box-shadow: -4px 0 13px 0 rgba(0, 0, 0, .14);
        color: $carousel-arrow-color;
        padding: $carousel-arrow-padding;
        height: 36px;
        width: 36px;

        display: flex;
        align-items: center;
        justify-content: center;

        @include bp(laptop) {
            @include font-size(26);
            width: 60px;
            height: 60px;
        }

        &:after {
            @include font-size(20);
        }

        &.swiper-button-prev {
            border-radius: $carousel-arrow-border-radius;

            .icon {
                position: relative;
                left: 1px;
            }
        }
    }
}


