// ----------------------------------------------------------------------------
// Button -- Transparent Pattern
// ----------------------------------------------------------------------------
//
// Buttons with transparent background that fill on hover; include this SCSS
// file in your project to bring in an additional button style, like so:
//
// @import 'components/elements/links/button/button';
// @import 'components/elements/links/button/button--transparent';
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Button Colors & Styles
// -------------------------------------

@each $color-name, $color-set in $button-color-palette {
    .button--#{""+$color-name} {
        &.button--transparent {
            background-color: rgba(map-get($color-set, color), 0);
            border-color: map-get($color-set, color);
            color: map-get($color-set, color);

            &:hover,
            a:hover & {
                background-color: rgba(map-get($color-set, color), 1);
                color: map-get($color-set, contrast);
            }
        }
    }
}
