// ----------------------------------------------------------------------------
// Header
// ----------------------------------------------------------------------------
//
// Basic site navigation, including a logo, main menu, and search.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$header-background-color: $white !default;
$header-logo-height-mobile: 50px !default;
$header-logo-height-desktop: 70px !default;
$z-index--header: 1000;
$header-overlay-color: rgba(0, 0, 0, .7);


// -------------------------------------
// Header
// -------------------------------------

.header {
    background-color: $header-background-color;
    position: relative;
    z-index: $z-index--header;
    border-bottom: $border--light;
}

.header__container {
    @include container;
}

.header__link--logo {
    height: $header-logo-height-mobile;
    vertical-align: baseline;
    width: auto;
    margin-right: gap(1.5);

    @include bp(laptop) {
        height: $header-logo-height-desktop;
    }

    img {
        height: 100%;
        width: auto;
    }
}

.header__link--menu {
    @include hamburger(22px, 22px, 2px);
    color: $primary-color;
    background: transparent;
    padding: 0;

    @include bp(laptop) {
        display: none;
    }
}


// -------------------------------------
// Header -- Default
// -------------------------------------

.header--default {
    .header__container {
        align-items: center;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        padding: 20px 0;

        @include bp(laptop) {
            display: grid;
            grid-template-columns: auto 1fr auto;
            padding: 0;

            .ie11 & {
                display: flex;
            }
        }
    }

    .header__link--logo {
        flex: none;
    }

    .header__link--menu {
        flex: none;
    }

    .menu--search {
        margin: 25px 15px;

        @include bp(tablet) {
            margin: 25px 20px;
        }

        @include bp(laptop) {
            margin: 25px gap(0.5);
        }

        @include bp(desktop) {
            margin: 25px 100px;
        }
    }

    .header__mobile-links {
        display: flex;
        align-items: center;

        svg {
            @include font-size(22);
            position: relative;
            top: 3px;
        }

        @include bp(laptop) {
            display: none;
        }
    }

    .header__link--cart {
        @include font-default;
        @include font-size(16);
        @include line-height(26);
        text-decoration: none;
        margin-right: gap(2);

        .menu__title {
            @include font-default;
            @include font-size(16);
            @include line-height(26);
            color: $primary-text-color;
        }

        .icon__cart {
            width: 22px;
            margin-right: 4px;
        }
    }

    .header__link--login,
    .header__link--contact {
        margin-right: gap(2);
    }

    .menu {
        &.js--isOpen {
            display: block;
            flex: 0 1 100%;
        }

        @include bp(laptop) {
            display: flex;
            flex: 0 1 auto;
        }
    }

    &.js--isOpen {
        &::after {
            background-color: $header-overlay-color;
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100vh;
            z-index: 100;
        }
    }

    .menu--desktop,
    .menu--search--desktop {
        display: none;

        @include bp(laptop) {
            display: block;
        }
    }

    .menu--mobile,
    .menu--search--mobile {
        display: block;

        @include bp(laptop) {
            display: none;
        }
    }
}

.header__nav-wrapper {
    background-color: $header-background-color;
    border-top: $border--light;
    border-bottom: $border--light;
    position: absolute;
    top: calc(100% - 0px);
    width: 100%;
    left: 0;
    margin: 0 0 60px;
    padding: 0;
    max-height: calc(100vh - 70px);
    overflow-y: scroll;
    display: none;


    .js--isOpen & {
        display: block;
        z-index: $z-index--header - 1;
    }

    @include bp(laptop) {
        position: initial;
        top: 0;
        width: auto;
        margin: 0;
        padding: 0;
        border: 0;
        overflow-y: visible;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

