// ----------------------------------------------------------------------------
// Button -- Text
// ----------------------------------------------------------------------------
//
// Buttons with a simple text CTA without any border or backgrounds.  Hit area
// of the button is just like the other buttons assist mobile users.
//
// @import 'base/button/button';
// @import 'base/button/button--text';
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Button Colors & Styles
// -------------------------------------

@each $color-name, $color-set in $button-text-color-palette {
    .button--#{""+$color-name} {
        &.button--text {
            position: relative;
            background-color: transparent;
            border: 0;
            color: map-get($color-set, color);
            text-transform: none;
            padding: 0;
            text-decoration-color: map-get($color-set, linkHover);
            text-underline-position: under;
            padding-right: 12px;
            outline-offset: 5px;

            &:hover,
            a:hover & {
                background-color: transparent;
                color: map-get($color-set, colorHover);
                text-decoration: underline;
                text-decoration-color: map-get($color-set, linkHover);

                .button__icon {
                    color: map-get($color-set, linkHover);
                }
            }

            .button__icon {
                position: relative;
                left: 10px;

                & .icon {
                    width: 18px;
                    height: 11px;
                }
            }
        }
    }
}
