// ----------------------------------------------------------------------------
// Tool Tip
// ----------------------------------------------------------------------------
//
// Provides a tool tip popup with text content.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$tooltip-background-color: $white !default;
$tooltip-box-shadow: $box-shadow !default;
$tooltip-color: $primary-text-color !default;
$tooltip-padding: 15px !default;
$tooltip-width: 200px !default;
$tooltip-z-index: $z-index--tooltip !default;

// -------------------------------------
// Tool Tip -- Base
// -------------------------------------
.tooltip {
    cursor: pointer;
    display: inline-block;

    @include bp(laptop) {
        position: relative;
    }
}

// -------------------------------------
// Tool Tip -- Default
// -------------------------------------
.tooltip__icon {
    color: $primary-color;
}

.tooltip__content {
    display: none;
    position: absolute;
    left: $tooltip-padding;
    right: -$tooltip-padding;
    width: calc(100% - #{$tooltip-padding} * 2);
    padding: $tooltip-padding;
    background-color: $tooltip-background-color;
    box-shadow: $tooltip-box-shadow;
    z-index: $tooltip-z-index;

    @include bp(laptop) {
        left: 50%;
        top: 100%;
        width: $tooltip-width;
        transform: translateX(-50%);
    }

    // Not everyone has hovering, so make it work for click
    &.tooltip__content--visible,
    &.tooltip__content--visible-locked {
        display: block;
    }
}

.tooltip__content--default {
    @include font-size(16);
    @include line-height(20);
    font-weight: 400;
}

// -------------------------------------
// Tool Tip -- Mini Cart
// -------------------------------------
.tooltip--mini-cart {
    .tooltip__content {
        bottom: -125px;
    }
}

