// ----------------------------------------------------------------------------
// Single Product Page
// ----------------------------------------------------------------------------
//
// Single Product page with grid product image slider, quantity select and
// add to cart button
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


.template-product {

    .page-section--product {
        margin-top: 0;
        margin-bottom: 0;
    }


    #MainContent {
        position: relative;

        @include bp(laptop) {

            &::after {
                position: absolute;
                top: 380px;
                z-index: -1;
                content: "";
                display: inline-block;
                width: 49%;
                height: 100%;
                max-height: 850px;
                background-image: url("data:image/svg+xml,%3Csvg width='1155' height='860' viewBox='0 0 1155 860' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M-18.3233 -1.94951e-05C-2.5572 -2.08734e-05 13.4375 1.80863 29.2038 5.65214L998.938 234.452C1092.16 256.382 1155 335.06 1155 430.017C1155 524.973 1092.39 603.425 998.939 625.582L29.2039 854.382C-31.576 868.851 -94.6407 855.06 -143.767 816.625C-192.894 778.19 -220.999 720.764 -220.999 658.817L-220.999 201.443C-220.999 139.495 -192.894 82.0693 -143.767 43.6346C-107.437 14.9216 -63.5655 -1.55399e-05 -18.3233 -1.94951e-05ZM-18.3233 766.886C-9.86903 766.886 -1.18604 765.982 7.2683 763.947L977.231 535.147C1039.15 520.451 1061.32 469.356 1061.32 429.791C1061.32 390.452 1039.38 339.13 977.231 324.434L7.49652 95.6346C-25.1783 87.9476 -59.2239 95.4083 -85.7294 115.982C-112.235 136.556 -127.316 167.53 -127.316 200.991L-127.316 658.59C-127.316 691.825 -112.235 722.799 -85.7294 743.599C-66.3073 758.747 -42.5438 766.886 -18.3233 766.886Z' fill='%2300E3E6' fill-opacity='0.1'/%3E%3C/svg%3E%0A");
                background-repeat: no-repeat;
                background-size: contain;
            }
        }

        @include bp(desktop) {
            &::after {
                top: 500px;
            }
        }
    }
}

// --------------------------------
// Product Hero
// --------------------------------
.product {
    width: 100%;
    margin: gap(4) 0;

    @include bp(laptop) {
        position: relative;
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .background-image {
        width: 100%;
        height: 100%;
        //min-height: 400px;
    }

    .form__label--product {
        margin-bottom: gap(.8)
    }

    .wishlist__container {
        margin-top: gap(1);

        .filts-wishlist-product-style .flits-wls-button-secondary {
            background-color: transparent !important;
            color: #001339 !important;
            border: 2px solid #001339 !important;
            border-radius: 25px;
            font-weight: bold;

            &:hover {
                background-color: #001339 !important;
                color: $white !important;
            }
        }

        .filts-wishlist-product-style .flits-wls-button-secondary:hover .flits-wls-icon-svg-color-blue {
            color: $white !important;
            fill: $white !important;
        }
    }
}

.flits-wls-snackbar .flits-wls-snackbar-header {
    background-color: #001339 !important;
}

// --------------------------------
// Product Image Slider
// --------------------------------
.product__images {
    position: relative;

    @include bp(tablet) {
        display: grid;
        grid-template-columns: 80px minmax(0, auto);
        grid-template-rows: auto;
        grid-column: 1;
        align-self: start;

        .ie11 & {
            display: flex;
            flex-direction: row-reverse;
        }
    }




    .swiper-arrow__container {
        position: absolute;
        &.swiper-arrow__container--prev {
            left: 0;
            z-index: 3;
            top: 50%;
            transform: translateY(-50%);

            @include bp(tablet) {
                width: 54px;
                left: 0;
                background: transparent;
                top: 0;
                transform: none;
            }

            .icon__arrow--left {
                @include bp(tablet) {
                    transform: rotate(90deg);
                }
            }
        }

        &.swiper-arrow__container--next {
            z-index: 3;
            right: 0;
            top: 50%;
            transform: translateY(-50%);

            @include bp(tablet) {
                padding-top: 10px;
                width: 54px;
                right: unset;
                left: 0;
                background: transparent;
                bottom: 0;
                top: unset;
                transform: none;
            }

            .icon__arrow--right {
                @include bp(tablet) {
                    transform: rotate(90deg);
                }
            }
        }
    }

    .swiper-arrow {
        //position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        padding: 0;
        width: 27px;
        height: 27px;
        background-color: $secondary-color;
        color: $white;

        &.swiper-button-disabled {
            display: none;
        }

        &.swiper-arrow__container--next {
            position: relative;
            top: 10px;
        }

        .icon {
            font-size: 15px;
            line-height: 15px;
            transform-origin: center;
        }
    }
}

.product-single__media-wrapper {

    @include bp(tablet) {
        padding: gap(1);
    }


    img,
    .product-single__media {
        border-radius: 20px;
        border: 1px solid rgba(200, 201, 207, .5);
        overflow: hidden;
        box-shadow: 0 5px 20px rgba(0, 19, 57, .05);
    }
}

.product__thumbnails {
    grid-row: 2;
    margin: 0;
    padding: 0;

    @include bp(tablet) {
        display: block;
        grid-column: 1;
        grid-row: 1;
        padding-top: gap(1);
    }

    .swiper-slide {
        @include aspect-ratio(1, 1);
        opacity: 0;
        border-radius: 2px;

        @include bp(tablet) {
            width: 56px !important;
        }

        &.swiper-slide-visible {
            opacity: 0.4;

            &.swiper-slide-thumb-active {
                opacity: 1;
                border-radius: 5px;
                border: 2px solid $border-color;
            }
        }
    }
}

// .swiper-button-disabled + .product__thumbnails {
//     @include bp(tablet) {
//         margin: gap(0) gap(1) 0 gap(3.5);
//     }
// }

.product__feature-image {
    padding-top: 15px;
    grid-row: 1;
    border-radius: 4px;
    overflow: hidden;

    @include bp(tablet) {
        padding-top: 0;
        grid-column: 2;
    }

    .product-tags--badge {
        top: gap(.5);
        left: gap(2.5);
        padding: gap(0.5) gap(1.5);

        @include bp(tablet) {
            top: 0;
        }
    }
}

.carousel--featured-product {
    width: 100%;
}

.carousel--product-thumbnails {
    //height: 550px;
    width: 100%;
    position: relative;
    padding: 0 30px;

    &.no-arrows--mobile {

        .swiper-arrow__container {
            display: none;
        }
    }


    @include bp(tablet) {

        &.no-arrows--desktop {

            .swiper-arrow__container {
                display: none;
            }
        }

        height: calc(448px + 90px);
        padding-bottom: 50px;
        padding-top: 30px;
        padding-right: 0;
        padding-left: 0;
        top: -30px;
    }
}

.product-single__media {
    margin: 0 auto;
    min-height: 1px;
    width: 100%;
    height: 100%;
    position: relative;

    iframe,
    img,
    > video,
    model-viewer,
    .shopify-model-viewer-ui,
    .plyr,
    .media-item {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 100%;
    }

    iframe,
    model-viewer,
    .media-video,
    .shopify-model-viewer-ui,
    .media-item,
    .plyr,
    .plyr__video-wrapper {
        height: 100%;
    }
}

.product-single__media--video {
  background-color: $background-color;
}

.product-single__thumbnail {
    display: block;
    height: 71px;

    @include bp(tablet) {
        width: 54px;
        height: 54px;
    }
}

.product-single__thumbnail-image {
    border: 0;
    border: 2px solid transparent;
    object-fit: cover;
    width: 100%;
    height: 100%;

    .active-thumbnail & {
        border-color: $border;
    }
}

.product__thumbnail-item {
    border-radius: 2px;
    overflow: hidden;
    position: relative;
}

.product-single__thumbnail-badge {
    @include font-size(35);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    border-radius: 50%;
}


// ---------------------------------
// Product Hero - Content
// --------------------------------
.product__content {
    padding: gap(1.5) 0;

    @include bp(laptop) {
        padding: gap(0) 0 0 gap(6);
        grid-column: 2;
    }

    form {
        padding: gap(2);
        background-color: rgba($background-color, .2);
        border-radius: 20px;
        margin-top: gap(2);

        @include bp(laptop) {
            padding: gap(3) gap(4);
        }
    }

    .product__price {

        .price__sale,
        .price__original {
            @include font-header;
            @include font-size(25);
            @include line-height(24);
        }

        .price__sale {
            margin-right: gap(.5);
            opacity: .5;
        }

        .price__original--sale,
        .price__original  {
            color: $black;
        }
    }

    .form__field--radio-text {

        .form__option-label {
            @include font-size(18);
            font-weight: 700;
            width: auto;
            height: auto;
            padding: gap(1) gap(2.5);
            border: 2px solid $border-color--light;
            background-color: $white;
            text-transform: inherit;
            box-shadow: none !important;
            border-radius: 5px;

        }

        input[type=radio]:checked+.form__option-label {
            background-color: rgba(0, 227, 230, 0.15);
            border: 2px solid $border-color;
            color: $black;

            &.swatch--disabled {
                color: $border-color--light;
                text-decoration: line-through;
            }
        }

        .swatch--disabled {
            display: none;
        }

        // cross-out the out-of-stock variants for the last variant level
        &:last-of-type {
            .swatch--disabled {
                &.swatch--out-of-stock {
                    display: inline-block;
                    color: $border-color--light;
                    text-decoration: line-through;
                }
            }
        }

        // if there are 3 variant options and the variant(s) on 2nd level is/are out-of-stock
        // then don't hide the out-of-stock variant()s on 2nd level
        &:nth-of-type(2) {
            &:not(:last-of-type){
                .swatch--disabled {
                    &.swatch--out-of-stock {
                        display: inline-block;
                    }
                }

                input[type=radio]:checked+.form__option-label {
                    &.swatch--disabled {
                        &.swatch--out-of-stock {
                            background-color: rgba(0, 227, 230, 0.15);
                            border: 2px solid $border-color;
                            color: $black;
                            text-decoration: none;
                        }
                    }
                }
            }
        }
    }

    .variant-title__wrapper {
        font-weight: bold;
        color: $black;
    }

    .variant-sku__wrapper {
        margin-bottom: gap(2);
    }
}

.product__title {
    margin: 0;

    @include bp(laptop) {
        margin: gap(1) 0;
    }
}

.product__tag {
    color: $white;
    background-color: $primary-color;
    text-transform: uppercase;
    padding: gap(.3) gap(.8);
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 5;

    @include bp(laptop) {
        padding: gap(.6) gap(1.1);
        top: 20px;
        right: 40px;
    }
}

.product__type {
    @include font-default;
    margin: 0;
}

.product__description {
    border-top: $border--light;
    margin-top: gap(2.5);
    margin-bottom: gap(2);
    padding-top: gap(2.5);

    p,
    ul {
        &:first-of-type {
            margin-top: 0;
        }
    }
}

.price__wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    .line-item__final-sale {
        @include font-size(16);
        display: inline-block;
        vertical-align: middle;
        border: $border--light;
        padding: gap(0.3) gap(.6);
        margin-left: gap(1.5);

        span {
           text-transform: uppercase;
        }

        .tooltip {
            margin-left: gap(.5)
        }
    }
}

