.product__form-wrapper {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;

    .add-to-cart {
        position: relative;
    }

    .form__input {
        border: 0;
        border-radius: 0;
        width: 70px;
    }
}

.product__quantity {
    margin-right: gap(3);
    margin-bottom: 0;

    .form__input-container {
        position: relative;
        width: 80px;
    }

    .cart__quantity {
        margin: 0;
    }
}

.product__container {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;

    &.product__container--footer {
        flex-flow: row nowrap;

        .btn--add-to-cart--quick {
            width: 100%;
        }
    }


    .selector-wrapper {
        width: 100%;
    }
}

// ----------------------------------------------------------------------------
// Product Sticky Cart
// ----------------------------------------------------------------------------


@include bp(laptop) {
    #productStickyContainer {
        margin-top: gap(2);
        padding-top: gap(2);
        min-height: 80px;
        border-top: 1px solid $grey;
    }
    .product__sticky-container {
        transform: translateY(0);
        .product__title {
            display: none;
        }

        .product__sticky--show & {
            position: fixed;
            width: 100%;
            background: white;
            bottom: 100%;
            left: 0;
            z-index: 1000;
            display: grid;
            grid-template-columns: [full-start] minmax(30px, 1fr) [main-start] minmax(0, 1280px) [main-end] minmax(30px, 1fr) [full-end];
            border-bottom: 1px solid #e5e5e5;
            padding: 11px 0;
            transform: translateY(100%);
            transition: transform 0.25s ease;

            .product__sticky {
                grid-column: main;
                display: grid;
                grid-template-columns: 1fr 2fr;
                align-items: center;
            }

            .product__title {
                display: block;
            }

            .form__label {
                display: none;
            }

            .product__container {
                align-items: center;
                justify-content: flex-end;
            }
        }

        .product__sticky--show.product__sticky--hide & {
            transform: translateY(0);
        }
    }
}
.product-sticky--mobile {
    display: none;
}

.cart__message--error {
    margin-top: gap(1);
    color: $red;
}

@include bp(tablet) {
    .product-sticky--mobile {
        display: flex;
        position: fixed;
        bottom: 20px;
        left: 0;
        width: 100%;
        justify-content: center;
        align-items: center;
        z-index: 10000;
        opacity: 1;
        transition: opacity 0.25s ease;

        .button--mobile-cart {
            background: $primary-color;
            color: $white;
            border: 2px solid transparent;
        }

    }

    #productStickyContainer:not(.product__sticky--show) + {
        .product-sticky--mobile {
            opacity: 0;
            pointer-events: none;
        }
    }
    @media (hover: hover) and (pointer: fine) {
        .product-sticky--mobile .button {
            &:hover {
                background: transparent;
                color: $primary-color;
                border-color: $primary-color;
            }
        }
    }
}


// ----------------------------------------------------------------------------
// Product Promotion
// ----------------------------------------------------------------------------
.product-promotion__container {
    margin: gap(2) 0 0;
    color: $black;
}
