// ----------------------------------------------------------------------------
// Featured Product
// ----------------------------------------------------------------------------
//
// A section that displays a featured product image, price, description and
// link to the product page.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';


// -------------------------------------
// Default Styles
// -------------------------------------
.featured-product {
    position: relative;

    @include bp(tablet) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: $grid-gutter;

        .ie11 & {
            display: flex;
        }
    }

    .card__tag {
        @include font-tags;
        padding: 0 gap(1);
        border-radius: 17px;
        position: absolute;
        top: -12px;
        left: 10px;
        z-index: 3;
        color: $white;
        background-color: $secondary-color;
        transition: transform 400ms ease;
    }


    .shopify-product-reviews-badge__link {
        display: block;
        margin-bottom: gap(1);
    }

    .featured-product__title-link {
        text-decoration: none;
        color: $primary-text-color;
    }
}

.featured-product__image {
    display: block;
    margin-bottom: gap(3);


    @include bp(laptop) {
        margin: 0 gap(8) 0 0;

        .ie11 & {
            width: 50%;
        }
    }

    .background-image {
        object-fit: contain;
    }
}

.featured-product__title {
    margin: 0 0 10px;
    border-bottom: 0;
}

.featured-product__description {
    border-top: 0;
}

.button__featured-product {
    margin-top: gap(3);
}
