// ----------------------------------------------------------------------------
// Main Blog
// ----------------------------------------------------------------------------
//
// A section with a Header, optional background image, button link and a
// repeating icon item (image, title and content).
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';




// ----------------------------------------------------------------------------
// Main Blog - Events
// ----------------------------------------------------------------------------
.main-blog--events {
	display: flex;
	flex-flow: row wrap;
	align-items: flex-start;
	grid-gap: gap(2.5);

	.blog__posts--events {
		margin-top: 0;
	}

	@include bp(laptop) {
		flex-flow: row nowrap;
	}

	.blog__posts--events {
		.card--event {
			@include bp(laptop) {
				display: contents;
			}
		}

		.card__image-wrapper {
			img {
				@include bp(laptop) {
					border-radius: 10px 10px 0 0;
				}
			}
		}
	}
}

.main-blog__wrapper {
	order: 2;
	width: 100%;

	@include bp(laptop) {
		order: 1;
	}
}

.main-blog--event-types {
	order: 1;
	padding: gap(3);
	width: 100%;
	background-color: rgba($light-grey, .2);
	border-radius: 20px;

	@include bp(laptop) {
		max-width: 315px;
		order: 2;
	}

	.event-types__list {
		padding: 0;
		margin: 0;
	}

	.event-types__heading {
		@include font-header;
		@include font-size(24);
		@include line-height(32);
		margin-top: 0;
		margin-bottom: gap(1);
		padding-bottom: gap(1);
		border-bottom: 1px solid $light-grey;
	}

	.event-types__list-item {
		list-style-type: none;

		a {
			color: $black;
			text-decoration: none;
			text-decoration-color: $secondary-color;
		}

		&.event-types__list-item--current,
		&:hover {

			a {
				text-decoration: underline;
				text-decoration-color: $secondary-color;
			}
		}

		&:not(:last-child) {
			margin-bottom: gap(1);
		}
	}
}

