// ----------------------------------------------------------------------------
// Icon Block
// ----------------------------------------------------------------------------
//
// A section with a Header, optional background image, button link and a
// repeating icon item (image, title and content).
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$icon-background-color: transparent;
$icon-text-color: $black;


// ------------------------------
// Icon Block -- Default Styles
// ------------------------------

.page-section--icon-block {
    // padding: gap(6) 0;
    background-color: $icon-background-color;

    @include bp(tablet) {
        text-align: center;
    }

    @include bp(laptop) {
        // padding: gap(10) 0;
    }
}

.icon-block {
    @include bp(tablet) {
        text-align: center;
    }

    .icon-block__header + &,
    .icon-block__content + & {
        margin-top: gap(3);

        @include bp(laptop) {
            margin-top: gap(5);
        }
    }

    .subtitle {
        display: block;
        margin-bottom: gap(1);
    }
}

.icon-block__header {
    color: $icon-text-color;
    // margin: 0 0 gap(3);
    margin: 0;
}

.icon-block__content {
    margin-top: gap(1);
    color: $icon-text-color;

    @include bp(laptop) {
        @include container;
        max-width: 846px;
        text-align: center;
    }
}

.icon-block__list {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;

    @include bp(tablet) {
        flex-flow: row nowrap;
        // margin: 0 0 gap(3);
    }

    &.icon-block__list--four {
        @include bp(tablet) {
            flex-wrap: wrap;
            row-gap: gap(4);
        }
    }
}

.icon-block__item {
    display: flex;
    padding: 0 0 gap(3);
    align-items: center;
    justify-content: flex-start;
    flex: inherit;

    @include bp(tablet) {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        // flex: 0 1 100%;
        flex: inherit;
        max-width: 425px;
        padding: 0 gap(1);
        flex: 100%;
    }

    // &:nth-child(2n) {
    //     @include bp(tablet) {
    //         flex: 100%;
    //     }
    // }

    &:last-child {
        margin-bottom: 0;
    }

    &.icon-block__item-spaced-out {
        @include bp(tablet) {
            padding: 0 gap(1.5);
        }
        @include bp(laptop) {
            padding: 0 gap(4.5);
        }
    }

    .icon-block__list--four & {
        @include bp(tablet) {
            padding: 0 gap(1.5);
            width: 25%;
            flex: inherit;
        }
        @include bp(laptop) {
            padding: 0 gap(4.5);
        }
    }
}

.icon-block__icon {
    display: block;
    width: auto;
    min-width: 71px;
    max-width: 90px;
    margin: 0;

    img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &.align-top {
        margin-top: 0;
        margin-bottom: auto;
    }

    @include bp(laptop) {
        width: 100%;
        height: 100%;
        min-width: 100px;
        max-height: 160px;
        max-width: 140px;

        &.align-top {
            margin: 0;
        }
    }
}

.icon-block__content-wrapper {
    text-align: left;
    margin-left: gap(2);

    @include bp(tablet) {
        margin-left: 0;
        text-align: center;
        min-width: 120px;
    }
}

.icon-block__title {
    @include font-primary;
    font-weight: 700;
    color: $icon-text-color;
    margin: 0 0 gap(1);

    @include bp(tablet) {
        margin: gap(1.5) 0 0;
    }
}

.icon-block__copy {
    color: $icon-text-color;
    margin: gap(1) 0 0;

    @include bp(tablet) {
        margin: gap(1.5) 0 0;
    }
}

.icon-block__button {
    margin: gap(1) 0 0;

    @include bp(tablet) {
        margin: gap(1.5) 0 0;
    }
}

// ------------------------------
// Icon Block -- Product
// ------------------------------

.page-section--icon-block--product {
    .accordion-panel__image {
        @include bp(tablet) {
            max-width: 100px;
        }
    }

    .icon-block__item {
        &.icon-block__item-spaced-out {
            flex: inherit;
        }
    }
}
