// ----------------------------------------------------------------------------
// Footer
// ----------------------------------------------------------------------------
//
// Lives at the bottom of every page to provide secondary navigation options.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$footer-background-color: $primary-color !default;
$footer-background-padding: gap(8) 0 !default;
$footer-content-margin: 0 0 $grid-gutter !default;
$footer-text-color: $white;

// -------------------------------------
// Footer Defaults
// -------------------------------------
.footer {
    padding: gap(6) 0;
    @include bp(laptop) {
        padding: $footer-background-padding;
    }

}

.footer__container {
    @include container;
    display: block;
}

.footer__content {
    display: inline-block;
    width: 100%;

    @include bp(laptop) {
        width: auto;
    }
}


// -------------------------------------
// Footer Content
// -------------------------------------


// -------------------------------------
// Legal
// -------------------------------------
.legal {

}

.legal__container {
    @include container;
    display: block;
}

.legal__content {
    @include bp(tablet) {
        display: flex;
        align-items: center;
    }
}




// -------------------------------------
// Footer -- Default
// -------------------------------------
.footer--default {
    background-color: $footer-background-color;

    // Main footer
    .footer__container--footer-menu {
        display: flex;
        flex-flow: row wrap;

        @include bp(laptop) {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: auto;
        }
    }

    .footer__container--logo {
        margin-bottom: gap(4);
    }

    .footer__container--legal {
        margin-top: gap(6);
        padding-top: gap(1.5);

        @include bp(laptop) {
            border-top: 1px solid $white;
        }
    }

    .footer__content--newsletter {
        order: 2;
        grid-column: 3;
        grid-row: 1;

        @include bp(laptop) {
            order: 1;
            padding-left: gap(1);
        } 

        .menu--social {
            
            .menu__list {
                flex-direction: row
            }

            .menu__link {
                @include font-default;
                color: $footer-text-color;
                padding: 0 gap(2) 0 0;
                margin: 0;

                &:hover {
                    color: rgba($footer-text-color, .7);
                    background-color: transparent;
                }
            }
        }
    }

    .footer__content--menu {
        order: 1;
        grid-column: 1 / span 2;
        grid-row: 1;
        margin-bottom: gap(4);

        @include bp(laptop) {
            order: 2;
            margin-bottom: 0;
        } 

        .menu--footer {
            margin-bottom: gap(1.5);
        }

        .menu__link {
            @include font-default;
            color: $footer-text-color;
            padding: 0 gap(2) 0 0;
            margin: 0;

            @include bp(laptop) {
                margin: 0;
                padding: 0;
            }

            &:hover {
                color: rgba($footer-text-color, .7);
                background-color: transparent;
            }
        }

        .css-grid--footer-menu {

            @include bp(laptop) {
                grid-gap: gap(5);
            }
        }
    }

    .footer__title {
        @include font-navigation;
        color: $footer-text-color;
        margin: 0 0 gap(1.5);

        &.footer__newsletter-title {
            @include font-default;
            @include font-size(18);
            @include line-height(24);
            font-weight: bold;
        }
    }

    .footer__newsletter-description {
        color: $footer-text-color;
    }

    .menu--footer {

        .menu__list {
            flex-flow: row wrap;

            .menu__item {
                width: 100%;

                &:not(:last-child) {
                    margin-bottom: gap(2);
                }
            }
        }
    }

    // Legal
    .legal__container {
        width: 100%;
        padding: 0;
        margin: 0;
    }

    .legal__content {
        flex: 0 0 auto;

        .menu--legal {
            margin-bottom: 0;

            .menu__list {
                display: flex;
            }

            .menu__item {
                width: auto;
            }

            .menu__link {
                margin-top: gap(1.5);
                margin-bottom: gap(1.5);
                margin-right: gap(2);
                text-decoration: none;

                @include bp(tablet) {
                    padding: 0;
                    margin: 0 0 0 gap(2);
                }

                &:hover {
                    .menu__title {
                        text-decoration: none;
                    }
                }
            }
        }

        .menu__title,
        .footer__copyright,
        .footer__address {
            @include font-default;
            @include font-size(14);
            @include line-height(20);
            color: $footer-text-color;
        }
    }

    .footer__copyright {
        @include font-size(16);
        color: $footer-text-color;

        @include bp(laptop) {
            margin: 0;
        }

        a {
            color: $footer-text-color;

            &:hover {
                color: rgba($footer-text-color, .7);
            }
        }
    }
    
    .footer__address {
        margin-left: auto;
    }
}
