// ----------------------------------------------------------------------------
// Return Prime
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';


// -------------------------------------
// Default Variables
// -------------------------------------

$button-color-palette: $color-palette !default;
$button-border-radius: 25px;
$button-transition: $anim !default;

.returnprime {
    grid-column: full;
    grid-row: 1;

    .returnprime__app {
        margin-bottom: 20px;
    
        .btn,
        .search__wrapper .form__wrapper .btn-primary,
        .customer .action__btns .back__btn,
        .customer .action__btns .next__btn {
            // Shopify Defaults
            appearance: none;
            border: 0;
            cursor: pointer;
            display: inline-block;
            line-height: 1;
            text-decoration: none;
            user-select: none;
            white-space: nowrap;
    
            // iamota Customizations
            border-radius: $button-border-radius;
            padding: 10px 40px;
            text-align: center;
            transition: $button-transition;
    
            @include font-header;
            @include font-size(16);
            @include line-height(24);
    
            // Disabled Styles live here because they override everything
            &:disabled {
                background-color: $white;
                border: 2px solid rgba(200, 201, 207, .5);
                color:  rgba(#C8C9CF, .5);
                pointer-events: none;
            }
        }
    
        .btn-primary,
        .customer .action__btns .back__btn {
            background-color: transparent !important;
            color: $primary-color !important;
            border: 2px solid $primary-color !important;
    
            &:focus {
                color: $primary-color !important;
                outline: 2px solid $secondary-color;
                outline-offset: 0;
            }
    
            &:hover,
            &:active,
            a:hover & {
                background-color: $primary-color !important;
                color: $white !important;
            }
    
            &:disabled,
            &.btn__disabled {
                background-color: $white !important;
                border: 2px solid rgba(200, 201, 207, .5) !important;
                color:  rgba(#C8C9CF, .5) !important;
                pointer-events: none !important;
            }
        }

        .customer .action__btns .next__btn {
            background-color: $primary-color !important;
            color: $white !important;
            border: 2px solid $primary-color !important;

            &:hover,
            &:active,
            a:hover & {
                background-color: transparent  !important;
                color: $primary-color !important;
            }
        }

        .customer .btn,
        .search__wrapper .form__wrapper .btn-primary {
            border-radius: $button-border-radius;
            text-align: center;
            transition: $button-transition;
            @include font-header;
            background-color: $primary-color !important;
            color: $white !important;
            border: 2px solid $primary-color !important;

            &:hover,
            &:active,
            a:hover & {
                background-color: transparent  !important;
                color: $primary-color !important;

                .Polaris-Icon {
                    .Polaris-Icon__Svg {
                        fill: $primary-color;
                    }
                }
            }

            &.exchange__btn {
                background-color: transparent !important;
                color: $primary-color !important;
                border: 2px solid $primary-color !important;

                .Polaris-Icon {
                    .Polaris-Icon__Svg {
                        fill: $primary-color;
                    }
                }
        
                &:focus {
                    color: $primary-color !important;
                    outline: 2px solid $secondary-color;
                    outline-offset: 0;
                }
        
                &:hover,
                &:active,
                a:hover & {
                    background-color: $primary-color !important;
                    color: $white !important;

                    .Polaris-Icon {
                        .Polaris-Icon__Svg {
                            fill: $white;
                        }
                    }
                }
            }
        }

        .Polaris-TextField__Backdrop {
            border: 1px solid $cyan !important;
            background-color: $white;
            border-radius: 0;

            &:after {
                border: none !important
            }
        }

        .select__field {
            select {
                border: 1px solid $cyan !important;
                background-color: $white;
            }
        }

        a{
            color: $bright-blue;
        }
    }
}