// ----------------------------------------------------------------------------
// Content Panels
// ----------------------------------------------------------------------------
//
// Content sections in blocks, side by side with media.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$content-panel-content-padding: gap(3) gap(10) !default;


// -------------------------------------
// Content Panel, General Styles
// -------------------------------------

.page-section--content-panel {
    & + .page-section--content-panel {
        // margin-top: 0;

        .content-panel--default {
            .content-panel__container {
                @include bp(tablet) {
                    padding-top: 0;
                }

                @include bp(laptop) {
                    padding-top: 0;
                }
            }
        }
    }
}

.content-panel {
    position: relative; 

    .content-panel__content {
        background: transparent;
        padding: gap(3) $grid-gutter--mobile 0;

        @include bp(tablet) {
            padding: gap(3) gap(6);
        }

        @include bp(laptop) {
            padding: $content-panel-content-padding;
        }
    }

    &.align-left {
        .content-panel__content {
            @include bp(desktop) {
                padding-left: gap(20);
            }
        }
    }

    &.align-right {
        .content-panel__content {
            @include bp(desktop) {
                padding-right: gap(20);
            }
        }
    }  

    .copy-block__text{
        max-width: 458px;
    }
    

    .content-panel__meta {
        img {
            .ie11 & {
                width: auto;
            }
        }
    }

    .content-panel__image {
        display: block;
    }

    @include bp(tablet) {
        .content-panel__meta {
            display: flex;
        }

        .content-panel__content {
            margin-bottom: 0;
        }
    }
}


// -------------------------------------
// Content Panel - Split
// -------------------------------------

.content-panel--split {
    @include bp(tablet) {
        .content-panel__container {
            display: flex;
        }

        .content-panel__content {
            flex: 0 1 62%;
        }

        .content-panel__meta {
            flex: 0 1 38%;
        }

         .content-panel__image {
            display: block;
        }

        &.align-left {
            .content-panel__content {
                order: 1;
            }

            .content-panel__meta {
                order: 2;
            }
        }

        &.align-right {
            .content-panel__content {
                order: 2;
            }

            .content-panel__meta {
                order: 1;
            }
        }
    }
}


// -------------------------------------
// Content Panel - Video
// -------------------------------------

.content-panel--video {
    .content-panel__meta {
        position: relative;
    }

    .content-panel__meta--video {

        &:focus {
            outline: $primary-color solid gap(.2);
            outline-offset: gap(.5);

            .video-block__control {

                .icon__play {
                    outline: $primary-color solid gap(.2);
                    outline-offset: gap(.5);
                }
            }
        }
    }
}


// -------------------------------------
// Content Panel - Project Specific
// -------------------------------------

.content-panel--default {

    padding: gap(3.5) 0;

    @include bp(laptop) {
        padding: gap(6.5) 0;
    }


    &:last-of-type {
        padding-bottom: 0;
    } 

    &:first-of-type {
        padding-top: 0;
    } 


    .content-panel__container {
        @include bp(tablet) {
            align-items: center;
        }
    }

    .content-panel__meta {
        height: 410px;
        overflow: hidden;

        @include bp(tablet) {
            flex: 0 1 38%;
            height: 500px;

            .ie11 & {
                max-width: 50%;
            }
        }

        @include bp(desktop) {
            height: 600px;
        }
    }

    .content-panel__content {
        .copy-block__title-tag {
            @include font-tags;
            text-transform: uppercase;
            font-weight: 800;
            color: $secondary-text-color;
            margin-bottom: gap(1);
        }

        .subtitle{
            display: block;
            margin-bottom: gap(1);
        }

        .copy-block__title {
            margin-top: 0;
            margin-bottom: gap(1.5);
        }

        .button--primary {
            margin-top: gap(3);
        }

        .modal__trigger {
            &.button {
                display: block;
            }
        }
    }
}

.content-panel__list {
    margin: gap(1.5) 0;
    margin-bottom: 0;
    padding: 0;
    list-style-type: none;
}

.content-panel__list-item {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    padding-bottom: gap(1);

    &:last-of-type{
        padding-bottom: 0;
    }

    .content-panel__list-item-icon {
        content: '';
        width: 10px;
        height: 10px;
        display: inline-block;
        background-color: $cyan; 
        border-radius: 100%;
        margin-top: 7px;
        margin-right: 9px;
    }
}

.content-panel__icon {
    display: block;
    color: $primary-text-color;
    margin-right: gap(1);

    &.icon {
        width: 40px;
        height: 40px;
        margin-bottom: gap(3);
    }
}


// -------------------------------------
// Content Panel - Background for Stacked
// -------------------------------------

.content-panel--stacked-background {
    @include bp(tablet){
        position: relative;
        
        &::after {
            position: absolute;
            top: 0;
            z-index: -1;
            content: "";
            display: inline-block;
            width: 100%;
            height: 100%;
            background-image: url("data:image/svg+xml,%3Csvg width='1155' height='860' viewBox='0 0 1155 860' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M-18.3233 -1.94951e-05C-2.5572 -2.08734e-05 13.4375 1.80863 29.2038 5.65214L998.938 234.452C1092.16 256.382 1155 335.06 1155 430.017C1155 524.973 1092.39 603.425 998.939 625.582L29.2039 854.382C-31.576 868.851 -94.6407 855.06 -143.767 816.625C-192.894 778.19 -220.999 720.764 -220.999 658.817L-220.999 201.443C-220.999 139.495 -192.894 82.0693 -143.767 43.6346C-107.437 14.9216 -63.5655 -1.55399e-05 -18.3233 -1.94951e-05ZM-18.3233 766.886C-9.86903 766.886 -1.18604 765.982 7.2683 763.947L977.231 535.147C1039.15 520.451 1061.32 469.356 1061.32 429.791C1061.32 390.452 1039.38 339.13 977.231 324.434L7.49652 95.6346C-25.1783 87.9476 -59.2239 95.4083 -85.7294 115.982C-112.235 136.556 -127.316 167.53 -127.316 200.991L-127.316 658.59C-127.316 691.825 -112.235 722.799 -85.7294 743.599C-66.3073 758.747 -42.5438 766.886 -18.3233 766.886Z' fill='%2300E3E6' fill-opacity='0.1'/%3E%3C/svg%3E%0A");
            background-position: left center;
            background-repeat: no-repeat;
            background-size: 80%;
        }

        &.stacked-background--fixed {
            &::after {
                background-position: left 195px;
            }  
        }

        &.left{
            &::after {
                transform: scaleX(-1);  
            }
        }
    }

    @include bp(laptop){
        &::after {
            background-position: left center;
        } 

        &.stacked-background--fixed {
            &::after {
                background-position: left 235px;
            }  
        }
        
    }

    @include bp(desktop){
        &::after {
            max-width: 2200px;
        }
    }
}
