// ----------------------------------------------------------------------------
// Collection
// ----------------------------------------------------------------------------
//
// A page template which displays a collection of products.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';


// ----------------------------------------------------------------------------
// Collection - Filter
// ----------------------------------------------------------------------------
.page-section--collection-page {
	margin-top: 0;

    @include bp(laptop) {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        #iamota-filters {
            width: 25%;
            padding-right: gap(4);
        }
    }

    .page-section--product__lists  {
    	margin-top: 0;
    	margin-bottom: 0;

        @include bp(laptop) {
            width: 75%;
        }

        .pagination {
            padding-top: gap(5);
        }
    }

    .product__lists__container {
    	padding: gap(2) gap(2) gap(3);
	    margin-top: 0;
	    margin-bottom: 0;
	    background-color: rgba($grey, 0.1);
	    border-radius: 20px;

	    @include bp(laptop) {
	    	padding: gap(4) gap(4) gap(6);
	    }
    }

    .product__lists__wrapper {
    	grid-column-gap: gap(1);
    	grid-row-gap: gap(3);

    	@include bp(laptop) {
    		grid-column-gap: gap(2);
    		grid-row-gap: gap(5);
    	}
    }

	.filter-menu {

		.has_group_selected {

			&.filter-group-not-shown {
				display: none;
			}
		}
	}

	.product__lists__header {
		margin-bottom: gap(4);
		grid-row-gap: 0;
	}

    .collection-name {

        a {

            &:hover {

                i.check-icon:before {
                    content: "";
                }
            }
        }
    }

    .sort-by__wrapper {
    	@include font-size(16);
    	@include line-height(24);
    	display: flex;
    	flex-flow: row nowrap;
    	color: $black;
    	font-weight: 700;

	    .pt-display-dropdown {
	    	.menu-trigger {
	    		@include font-size(16);
    			@include line-height(24);
    			color: $black;
    			font-weight: 700;
	    		border: none;
	    		display: inline-block;
	    		padding: 0 28px 0 5px;

		    	&::after {
		    		content: "";
		    		top: 9px;
		    		background-image: url("data:image/svg+xml;base64,");
		    		width: 11px;
		    		height: 7px;
		    		background-size: contain;
		    		background-repeat: no-repeat;
		    	}
		    }
	    }

        .sort-by__label {
            @include bpgd(mobile) {
                width: gap(10);
            }
        }
    }

    .filter-results__wrapper {
    	@include font-size(16);
    	@include line-height(24);
    	font-weight: 700;
    	color: $black;
    	text-align: left;

    	@include bp(tablet) {
    		text-align: right;
    	}
    }


    i.check-icon:before {
        content: "";
        background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMjAiIGhlaWdodD0iMjAiIHZpZXdCb3g9IjAgMCAyMCAyMCIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4NCjxyZWN0IHdpZHRoPSIyMCIgaGVpZ2h0PSIyMCIgZmlsbD0id2hpdGUiLz4NCjxyZWN0IHg9IjEuNSIgeT0iMS41IiB3aWR0aD0iMTciIGhlaWdodD0iMTciIHJ4PSIxLjUiIHN0cm9rZT0iYmxhY2siLz4NCjwvc3ZnPg0K");
        background-size: contain;
        width: 20px;
        height: 20px;
    }

    .selected i.check-icon:before {
        content: "";
        background-image: url("data:image/svg+xml;base64,");
    }

    .filter-group {

        h4 {
            @include font-default;
            @include font-size(20);
            @include line-height(24);
            font-weight: 700;
            color: $black;
            padding-bottom: gap(1);

            &::after {
            	content: "";
                margin-right: 30px;
                top: 10px;
                width: 12px;
                height: 7px;
                background-size: contain;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml;base64,");
            }
        }

        li.selected {

        	a {
        		color: $black;		
        	}
        }

        &.pt-group-expanded {

        	h4 {

        		&::after {
        			content: "";
        			background-image: url("data:image/svg+xml;base64,");
        			top: 10px;
        		}
        	}
        }

        @include bpgd(mobile) {

            h4 {
                border-bottom: none;
                font-size: 18px;
                font-weight: bold;
            }

            h4,
            .scroll-content {
                padding-right: 30px;
                padding-left: 30px;
            }
        }
    }

    .filter-group+.filter-group h4 {


        &::after {
            top: 20px;
        }
        
        @include bpgd(mobile) {
            border-top: 1px solid #ddd;
            padding-top: 20px;
            border-top: 1px solid #ddd;

            &::after {
                top: 30px;
            }
        }
    }

    .filter-results__heading {
    	@include font-default;
        @include font-size(20);
        @include line-height(24);
        margin-top: 0;
        margin-bottom: 0;
        color: $black;
        font-weight: 700;
    }

    .pill__list__heading {
        margin-bottom: gap(1.5);
    }

    .filter-group h4:after {
        color: #000;
    }

    .pill__list {
        margin-top: 10px;
        margin-bottom: 20px;

        i.check-icon:before {
            background-image: url("data:image/svg+xml;base64,");
            background-size: 8px;
            background-position: 6px 6px;
        }

        .collection-name {
            display: inline-block;
            background-color: transparent;
            border-radius: 3px;
            border: 3px solid $border-color;
            padding: 0 7px 0px 15px;
            margin-bottom: 4px;

            a {
                @include font-size(16);
                @include line-height(24);
                font-weight: bold;
                color: $primary-color;
                text-decoration: none;
            }
        }

        .filter-menu__clear {
            color: $black;

            &:visited {
                color: #000;
            }

            @include bpgd(mobile) {
                display: none;
            }
        }
    }

    .filter-group .collection-count {
        position: relative;
    }

    .filter-menu-none-selected li a, .has_collection_counts a {
        margin-right: 5px;
    }

    .filter-menu__product-numbers {
    	@include font-size(15);
    	@include line-height(24);
        margin-right: 0;

        @include bp(laptop) {
        	margin-right: 30px;
        }
    }
}

.filter-menu__popup__trigger {
    display: none;

    @include bpgd(mobile) {
        display: block;
        width: 100%;
        border: 1px solid $primary-color;
        text-align: center;
        font-size: 16px;
        padding: 10px 10px;
        margin: 35px 0;
        text-decoration: none;
        color: $primary-color;
    }
}

.filter-menu__popup {
    padding-top: 12px;
    padding-bottom: 20px;
    
    @include bp(laptop) {
        padding-bottom: 70px;
        margin-right: 30px;
    }

    .filter-menu__popup__footer {
        display: none;
    }

    .filter-menu__popup__close {
        display: none;
    }



    .collection-name {

        a {
            @include font-size(15);
            @include line-height(24);
         	font-weight: 400;
            margin-bottom: 10px;
            color: $black;
        }
    }

    @include bpgd(mobile) {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 9999;
        background-color: #fff;
        overflow-y: auto;
        display: none;
        visibility: visible;

        &.active {
            display: block;
            visibility: visible;
        }



        .filter-menu__popup__close {

            &:not(.filter-menu__popup__footer--button) {

                cursor: pointer;
                display: block;
                position: absolute;
                top: 10px;
                right: 30px;
                color: #000;
            }
        }

        .filter-results__heading,
        .pill__list {
            text-align: center;
        }

        .pill__list {
            padding-bottom: 15px;
            border-bottom: 1px solid #ddd;
        }

        .scroll-content {
            padding-bottom: 15px;
        }

        .filter-menu {
            border-bottom: 1px solid #ddd;
        }

        .filter-menu__popup__footer {
            display: flex;
            flex-flow: row nowrap;
            position: fixed;
            bottom: 0;
            left: 0;
            width: 100%;

            a {
                display: block;
                width: 50%;
                text-decoration: none;
                padding: 12px 60px;
                text-align: center;
                font-size: 16px;
                font-weight: bold;

                &.filter-menu__popup__close {
                    color: #fff;
                    background-color: $background-color--dark;
                    border: 0.5px solid #5D90C5;
                }

                &.filter-menu__clear {
                    background-color: #fff;
                    border: 0.5px solid rgba(0, 0, 0, 0.2);
                    color: $black;
                }


            }
        }
    }

    @include bp(laptop) {
    	padding-top: 0;
    }


}

.fixedHeight {

  @include bpgd(mobile) {

    height: 100vh;
    overflow: hidden;
  }
}