// ----------------------------------------------------------------------------
// Content Cards
// ----------------------------------------------------------------------------
//
// This is a section that displays a default card consisting of image,
//title, content and button. It displays as either a 1, 2 or 3 column grid.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

.content-cards__content {
    @include bp(laptop) {
        @include container--wysiwyg;
        margin-left: 0;
    }
}

.css-grid--content-cards {
    margin-top: gap(4);
}

.partner-cards {
    @include bp(tablet) {
        text-align: center;
    }

    .partner-cards__header + & {
        margin-top: gap(3);

        @include bp(laptop) {
            margin-top: gap(5);
        }
    }

    .partner-cards__header {
        color: $black;
        margin: 0;
    }

    .subtitle {
        display: block;
        margin-bottom: gap(1);
    }

    .css-grid--content-cards {
        grid-gap: gap(0.7);

        @include bp(laptop) {
            grid-gap: gap(3);
        }
    }
}
