// ----------------------------------------------------------------------------
// Cart Confirmation Modal
// ----------------------------------------------------------------------------
//
// A confirmation modal triggered by and add to cart button. Displays Product info,
// and view cart/checkout buttons
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -----------------------------------
//  Cart Confirmaton Alert
// -----------------------------------
.cart-confirmation__message {
    @include font-size(16);
    padding: gap(.2) gap(1.5);
    margin: 0 gap(3) gap(1) gap(0);
    background-color: $background-color--dark;
    color: $white;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    @include bp(tablet) {
        grid-column: 1;
        grid-row: 1;
        margin: 0 0 gap(2);
    }

    .icon {
        color: $white;
        margin-left: gap(1);
        width: 18px;
        height: 18px;
    }
}

// -----------------------------------
//  Cart Confirmaton Item
// -----------------------------------
.cart-confirmation__item {
    border-bottom: $border--light;
    padding-bottom: $grid-gutter--mobile;

    @include bp(laptop) {
        grid-column: 1;
        grid-row: 2;
        padding-bottom: 0;
        padding-right: gap(3);
        border-bottom: 0;
        border-right: $border--light;
    }

    .cart-info__item-count {
        display: flex;
    }
}

.cart-confirmation__content {
    padding-top: $grid-gutter--mobile;

    @include bp(laptop) {
        grid-column: 2;
        grid-row: 2;
        padding: 0 0 0 gap(3);
    }

    .button-collection {
        display: flex;
        align-items: center;
        justify-content: flex-end;

        .button + .button {
            margin-left: gap(1.5);
        }

        @include bp(laptop) {
            flex-flow: row nowrap;
        }
    }

    .button__checkout,
    .button__edit-cart {
        width: calc(50% - 8px);
        padding: 14px 15px 13px;
    }

    .button__edit-cart {

        &:focus {
            color: $primary-color;
        }

        &:hover:focus {
            color: $white;
        }
    }
}

