// ----------------------------------------------------------------------------
// Menu
// ----------------------------------------------------------------------------
//
// Navigation patterns for the site; the default functionality is simply to pare
// the component down to its most base styles. Leverage additional variants to
// power up the menu into more complex patterns.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$menu-background-color: transparent !default;
$menu-item-margin: 0 !default;
$menu-link-background-color: transparent !default;
$menu-link-color: $primary-color !default;
$menu-link-padding: 30px 20px !default; // use padding because menu items can have backgrounds



// -------------------------------------
// Menu
// -------------------------------------
.menu {
    display: block;
}

.menu__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.menu__item {
    margin: $menu-item-margin;
}

.menu__link {
    color: $menu-link-color;
    cursor: pointer;
    display: block;
    text-decoration: none;

    &.menu__link--account {
        color: $bright-blue;
    }

     .icon__cart {
        width: 22px;
        margin-right: 4px;
        position: relative;
        top: 3px;
    }
}

.menu__title {
    @include font-navigation;
    line-height: 1;
    margin: 0;
}


// -------------------------------------
// Menu -- Default
//
// Defaults to a horizontal flow typical
// of most site menus.
// -------------------------------------
.menu--default {
    background-color: $menu-background-color;

    .menu__list {
        display: flex;
        flex-flow: column nowrap;

        @include bp(laptop) {
            flex-flow: row nowrap;
        }
    }

    .menu__link {
        background-color: $menu-link-background-color;
        padding: 10px $grid-gutter--mobile;
        transition: $anim;

        @include bp(laptop) {
            padding: gap(3) gap(1);
        }

        @include bp(desktop) {
            padding: $menu-link-padding;
        }

        &.menu__link--active {
            background-color: $background-color;
        }

        &:hover {
            background-color: $background-color;
            color: $menu-link-color;
        }
    }
}

// -------------------------------------
// Menu -- Secondary Nav
// -------------------------------------
.menu--secondary {
   margin: gap(1.5) $grid-gutter--mobile;
   padding: gap(3) 0 0;
   border-top: $border--light;
   display: none;

   @include bp(tablet) {
        display: block;
   }

   @include bp(laptop) {
        margin: 0 $grid-gutter--mobile;
        padding: 0;
        border: 0;
        margin-right: 0;
   }

   .menu__list {
        display: flex;

        flex-flow: column nowrap;

        @include bp(laptop) {
            flex-flow: row nowrap;
             align-items: center;
        }
   }

    .menu__item {
        display: inline-block;
        margin-right: gap(2);
        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: 0;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
    .menu__item--cart {
        display: none;

        @include bp(laptop) {
            display: inline-block;
        }
    }

    .menu__link {
        font-weight: 700;

        &:hover {
            color: $grey;

            a {
                color: $grey;
            }
        }

        a {
            @include font-default;
            @include font-size(16);
            @include line-height(26);
            font-weight:700;
            text-decoration: none;
            color: $menu-link-color;
        }
    }

    .menu__title {
        @include font-default;
        @include font-size(16);
        @include line-height(26);
    }
}


// -------------------------------------
// Menu - Globo Mega Menu
// -------------------------------------

.gm-menu-installed {
    
    .gm-menu {

        &#desktopNav {
            justify-content: space-between;
            max-width: 1200px;
            margin: 0 auto;
            margin-top: -5px;
            
            .gm-item.gm-level-0>.gm-target {
                padding: 15px 10px !important;
            }
        }

        .gm-tab-content {
            align-items: flex-start;
        }
    }

    .gm-links,
    .gm-dropdown {

        a {

            &:hover {
                text-decoration: underline;
            }
        }
    }
    
    .gm-dropdown {
        right: 0;
    }

    .gm-submenu-dropdown {
        &:last-of-type {
            .gm-dropdown {
                left: auto;
            }
        }
    }
}


#mobileNav {

    .gm-image {
        display: none !important;
    }
}