// ----------------------------------------------------------------------------
// Menu -- Dropdown
// ----------------------------------------------------------------------------
//
// Navigation for the site, consisting of an accordion-styled mega
// mobile menu and a hover-style dropdown desktop menu. Inherits from menu.
//
// @import 'components/site-elements/menu/menu';
// @import 'components/site-elements/menu/menu--dropdown';
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$menu-dropdown-background-color: $white !default;
$menu-dropdown-min-width: 170px !default;
$menu-dropdown-padding: gap(1.5) !default;
$menu-dropdown-item-margin: 0 0 gap(.5) !default;
$menu-dropdown-link-color: $primary-text-color !default;
$menu-dropdown-link-hover: $secondary-color;

// Transition Variables: property name | duration | timing function | delay
$menu-dropdown-transition: 200ms all .2s ease, visibility 0s linear .01s, z-index 0s linear 0s !default;



// ------------------------------
// Menu -- Dropdown
// ------------------------------
.menu--dropdown {
    @include bp(laptop) {
        .menu__list {
            align-items: flex-end;
        }
    }
}

.menu__item--dropdown {
    transition: $anim;

    .menu__link {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include bp(laptop) {
            display: inline-block;
        }
    }

    .icon {
        transition: $anim;
    }

    .dropdown__icon {
        position: relative;
        display: block;
        color: $white;
        width: 24px;
        height: 24px;
        background-color: $secondary-color;
        border-radius: 20px;

        &::before,
        &::after {
            background: currentColor;
            content: '';
            display: block;
            height: 1px;
            border-radius: 1px;
            position: absolute;
            width: 10px;
        }

        &::before {
                top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &::after {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(90deg);
            visibility: visible;
        }

        @include bp(laptop) {
            display: none;
        }
    }

    // For mobile, turn the dropdown into a toggle
    &.js--isOpen {
        .dropdown {
            display: block;
        }

        .dropdown__icon {
            &::after {
                visibility: hidden;
            }
        }
    }

    // Exclude dropdown hover to large screens
    @include bp(laptop) {
        &.js--isOpen {
            background-color: $menu-dropdown-background-color;

            .menu__link {
                color: $menu-dropdown-link-color;
                background-color: $background-color;
            }

            .dropdown {
                opacity: 1;
                transform: translateY(0%);
                pointer-events: all;
                z-index: 1;
            }
        }
    }
}


// ------------------------------
// Dropdown Styles
// ------------------------------
.dropdown {
    display: none;
    padding: gap(1.5) gap(2.5);

    @include bp(laptop) {
        background-color: $menu-dropdown-background-color;
        display: block;
        min-width: $menu-dropdown-min-width;
        border: $border--light;
        position: absolute;
        top: 100%;
        padding: $menu-dropdown-padding;

        // Transition animation
        opacity: 0;
        transition: $menu-dropdown-transition;
        pointer-events: none;
        z-index: -1;
    }
}

.dropdown__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.dropdown__item {
    margin: 0 0 gap(2);

    @include bp(laptop) {
         margin: $menu-dropdown-item-margin;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.dropdown__link {
    @include font-primary;
    @include font-size(16);
    @include line-height(26);
    text-decoration: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    line-height: 1;


    &.dropdown__link--active {
        font-weight: 700;
        color: $menu-dropdown-link-hover;
    }

    @include bp(laptop) {
        color: $menu-dropdown-link-color;

        &:hover {
            font-weight: 700;
            color: $menu-dropdown-link-hover;
            text-decoration: underline;
        }
    }
}

// ------------------------------
// Mega Nav Dropdown Styles
// ------------------------------
.dropdown__mega-nav {
    width: 100%;
    left: 0;
}

.mega-nav {
    margin: 0;

    @include bp(laptop) {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: $grid-gutter;
        padding: gap(4) 0;
        margin: 0 auto;
    }

    .dropdown__item {
        list-style-type: none;
        padding: 0;
    }
}

.mega-nav__title {
    @include font-tags;
    display: block;
    margin-bottom: gap(1);
}

.dropdowm__item--image {
    display: none;

    @include bp(laptop) {
        display: block;
        grid-column: 4;
    }

    .dropdown-image {
        height: 200px;
        margin-bottom: gap(1);
    }

    .dropdown__link {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
}
