// ----------------------------------------------------------------------------
// Page Header
// ----------------------------------------------------------------------------
//
// Primary header for the site. Contains title and description, with image
// variant.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$page-header-image-height                 : 300px;
$page-header-image-height--desktop        : 434px;
$page-collection-header-background-color  : rgba($cyan, .2);;


// -------------------------------------
// Page Header -- Default
// -------------------------------------
// generic page header styles with no background image
.page-header {
    .page-header__content {
        @include font-primary;
        padding: $grid-gutter 0;

        @include bp(laptop) {
            padding: ($grid-gutter * 2) 0;
        }
    }

    .page-header__title {
        margin: 0;
        margin-bottom: 5px;

        @include bp(laptop) {
            margin-bottom: 10px;
        }
    }

    .page-header__text {
        @include container--wysiwyg;
        text-align: left;
        margin-left: 0;
    }

    .page-header__button {
        margin-top: 20px;

        @include bp(laptop) {
            margin-top: 30px;
        }

        &:hover {
            color: $secondary-color;
        }
    }
}

.page-header--left {
    .page-header__title {

        @include bp(laptop) {
            text-align: left;
        }
    }
}

.page-header--text {
    @include container;
}


// -------------------------------------
// Page Header -- Image
// -------------------------------------
.page-header--image {

    .page-header__mobile-image {
        display: block;

        @include bp(tablet) {
            display: none;
        }
    }

    .background-media--image {
        height: auto;
        display: block;

        .background-media__background {
            @include aspect-ratio(1, 0.8);
            display: none;
            position: relative;

            .background-image {
                position: absolute;
                object-position: center right;

                @include bp(laptop) {
                    object-position: center center;
                }
            }

            @include bp(tablet) {
                display: block;
                @include aspect-ratio(1, 0.3);
            }
        }

        @include bp(laptop) {
            display: grid;
            height: $page-header-image-height--desktop;
            grid-template-columns: [gutter-start] minmax(60px, 1fr) [content-start] minmax(0, #{math.div($max-width, 3)}) [center] minmax(0, #{math.div($max-width, 3)}) [content-end] minmax(0, #{math.div($max-width, 3)}) [content-full] minmax(60px, 1fr) [gutter-end];
        }
    }

    .background-media__content {
        padding: gap(1.5);
        max-width: 620px;
        text-align: left;
        grid-column: content;
        grid-row: content;
        border-radius: 20px;

        @include bp(laptop) {
            padding: 0;
        }
    }

    .page-header__title {
        color: $black;

        &.page-header__title--outline {
            @supports((text-stroke: 1px black) or (-webkit-text-stroke: 1px black)) {
                text-stroke: 1px $black;
                -webkit-text-stroke: 1px $black;
                color: transparent;
            }
        }
    }

    .page-header__text {
        width: 100%;
        margin: 0;
        text-align: left;
        color: $primary-text-color;
    }

    .button--page-header {
        margin-top: gap(2);
    }

    &.page-header--white {
        .background-media__content {
            background-color: $white;

            @include bp(laptop) {
                padding: gap(4);
            }
        }
    }
}

// -------------------------------------
// Page Header -- Video
// -------------------------------------
.page-header--video {
    .video-block {
        height: $page-header-image-height;

        @include bp(laptop) {
            height: $page-header-image-height--desktop;
        }
    }

    .page-header__title {
        color: $white;
    }

    .background-media__content {
        text-align: center;
        align-self: end;
    }

    .page-header__text {
        @include font-size(18);
        @include line-height(28);
        font-weight: 600;
        color: $white;
        margin: 0 auto;
        text-align: center;
    }

    .video-block__control {
        color: $primary-color;
        background-color: $white;
        padding: 4px;
        position: absolute;
        top: gap(2);
        left: gap(2);
        z-index: 1;
        border-radius: 30px;
        align-items: center;
        justify-content: center;

        @include bp(laptop) {
            top: initial;
            left: initial;
            bottom: gap(2);
            right: gap(2);
        }

        &:focus {
            outline: none;
            box-shadow: 0 0 0 5px rgba($primary-color, 0.5);
        }
    }

    .video-block__control--pause {
        @include font-size(40);
        display: flex;
    }

    .video-block__control--play {
        @include font-size(40);
        display: none;
    }

    .video-block--is-paused {
        & .video-block__control--play {
            display: flex;
        }

        & .video-block__control--pause {
            display: none;
        }
    }
}

// -------------------------------------
// Page Header -- Collection
// -------------------------------------
.page-header--collection {
    // border-bottom: $border--light;
    background-color: $page-collection-header-background-color;

    @include bp(laptop) {
        max-height: 450px;
        overflow: hidden;
    }

    .background-image {
        object-fit: cover;
        height: 100%;
        width: 100%;

        .ie11 & {
            height: auto;
        }
    }

    .breadcrumbs {
        padding: $grid-gutter--mobile;

        @include bp(laptop) {
            padding: $grid-gutter 0;
        }
    }

    .page-header__content {
        padding: gap(4) $grid-gutter--mobile;

        @include bp(laptop) {
            max-width: 470px;
            padding: gap(4) 0;
            margin-right: $grid-gutter;
        }
    }

    .page-header__text {
        width: 100%;
    }

    // Desktop Grid Layout
    // -------------------------------------
    @include bp(laptop) {
        display: grid;
        grid-template-columns: minmax($grid-gutter, 1fr) repeat(2, minmax(0, ($max-width * 0.5))) minmax($grid-gutter, 1fr);
        // grid-template-rows: ($grid-gutter * 2) 1fr ($grid-gutter * 2);
        grid-template-rows: 0 1fr 0;
        align-items: center;
    }

    .page-header__media {
        display: flex;
        -ms-grid-column: 3;
        -ms-grid-column-span: 2;
        -ms-grid-row: 1;
        -ms-grid-row-span: 2;
        grid-column: 3 / -1;
        grid-row: 1 / -1;

        height: 260px;

        @include bp(tablet) {
            height: 300px;
        }

        @include bp(laptop) {
            min-height: 450px;
            max-height: 100%;
            height: 100%;
        }

        .ie11 & {
            overflow: hidden;
        }
    }

    .breadcrumbs {
        grid-row: 1;
        grid-column: 2;
        -ms-grid-row-align: center;
    }

    .page-header__content {
        grid-row: 2;
        grid-column: 2;
        -ms-grid-row-align: center;
    }
}

// -------------------------------------
// Page Header -- Search
// -------------------------------------
.page-header--search {
    background-color: $page-collection-header-background-color;
    margin-bottom: gap(3);

    .page-header__content {
        margin-bottom: 0;
        padding: gap(4) 0;
    }

    .page-header__title {
        margin: 0;
    }

    .page-header__text {
        @include font-caption;
    }
}


// -------------------------------------
// Page Header -- Password
// -------------------------------------
.page-header--password {

    .page-header__content {
        padding-bottom: gap(3);
    }

    .page-header__title {
        margin-bottom: gap(5);
    }
}


// -------------------------------------
// Page Header -- Login Page
// -------------------------------------
.page-header--account-form {
    .page-header__subtitle {
        margin-top: 0;
    }

    .page-header__content {
        @include bp(laptop) {
            padding: gap(8) 0;
        }
    }
}

// -------------------------------------
// Page Header -- Register Page
// -------------------------------------
.page-header--register-page {
    .page-header__description {
        margin: gap(2) auto;

        & + .page-header__title {
            margin-top: gap(5);
        }
    }

    .page-header__title {
        margin-bottom: 0;
    }
}