// ----------------------------------------------------------------------------
// Account Details
// ----------------------------------------------------------------------------
//
// For Page template within Account screen including  order details
// and order table and account address
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Default Elements
// -------------------------------------
#account {

    .flits-account-container .flits-address-card.flits-new-address-card svg {
        fill: $primary-color;
    }

    .flits-account-container .flits-address-card.flits-new-address-card p {
        background-color: $primary-color;
    }

    .flits-account-container .flits-address-card .flits-triangle-wrapper .flits-triangle {
        border-color: rgba(255,255,255,0) rgba(255,255,255,0) rgba(249,0,0,0) $primary-color;
    }

    .flits-account-container .flits-menus-list-box .flits-menu-items .flits-menu-item .flits-menu-badge {
        background-color: $cyan;
        color: $white !important;
    }

    .flits-account-container .flits-navigation-header .flits-user-avatar {
        background-color: $cyan;
    }

    .flits-account-container .flits-menus-list-box .flits-menu-items .flits-menu-item:before {
        background: $cyan;
    }

    .flits-account-container .flits-address-card .flits-address-card-action ul .flits-action-item.flits-address-default-btn {
        background-color: $primary-color;
    }

    .flits-greeting-header,
    .flits-navigation-header {

        .store-credit-embedded-button {
            color: white;
        }

        .store-credit-embedded-widget {
            text-align: center;
        }
    }

    .flits-account-container .flits-menu-nav {
        top: 0;
    }

    .flits-desktop-view {
        
        .flits-greeting-header {
            float: right;
        }
    }
}


.page-section--account-details {
    .address {
        margin: gap(2) 0;
    }
}

.account-details__title {
    margin-top: 0;
    margin-bottom: gap(2);

    &.account-details__title-address {
        margin-top: gap(3.5);

        @include bp(tablet) {
            margin-top: gap(6.5);
        }
    }
}

.form-success {
    padding-bottom: gap(3);
}

.breadcrumbs-container {
    margin-bottom: gap(2);
}

.button__log-out {
    margin-top: gap(2);
}

.form__cancel-link {
    display: block;
    margin-top: gap(2);
}


// -------------------------------------
// Order Table Styles
// -------------------------------------
.order-table__table {
    width: 100%;
    border-collapse: collapse;

    th,
    td {
        border: 0;
    }

    tr:nth-child(2n) {
        background: $white;
    }
}

tr.order-table__header {
    border-bottom: $border--light;
}

.order-table__header-item {
    @include font-caption;
    color: $primary-text-color;
    text-align: left;
    padding: gap(1) 0 0;

    &:last-of-type {
        text-align: right;
        @include bp(tablet) {
            padding-right: 0;
        }
    }

    @include bp(tablet) {
        text-align: left;
        padding: gap(0) gap(1) gap(2) 0;
    }
}

td.order-table__item {
    padding: gap(1) 0 0;
    min-width: 100px;

    @include bp(tablet) {
        text-align: left;
        padding: gap(2) gap(.5);
    }

    &:last-of-type {
        text-align: right;

        padding: gap(1) 0 gap(1);

        @include bp(tablet) {
            padding: gap(2) 0;
        }
    }

    &:not(.order-table__item-price) {
        @include font-size(16);
    }
}

.order-table__row {
    border-top: $border--light;

    @include bp(tablet) {
        border-top: 0;
    }
}

.order-table__footer {
    .order-table__row {
        border-top: 0;
        width: 100%;

        &:first-of-type {
           border-top: $border--light;
        }

        & .small--hide {
            display: none;

            @include bp(tablet) {
                display: table-cell;
            }
        }

        .order-table__item {
            padding: gap(1) 0 0;

            @include bp(tablet) {
                padding: gap(2) 0 0;
            }
        }
    }
}

// -------------------------------------
// Address Styles -- Default
// -------------------------------------
.address {
    & p {
        margin-bottom: 0;
    }

    .address__title {
        margin-bottom: gap(.5);
    }

    .button-collection--address {
        align-items: center;
        margin-bottom: 0;
        margin-top: gap(1.5);

        .form__button {
            margin-right: gap(2);
        }
    }
}

#address_form_new {
    margin-top: gap(3);
}

.form--address-edit {
    margin-top: gap(2);
}

.page-section--account-details {
    a {
        @include font-size(16);
    }
}

.template-addresses {
    .form__wrapper {
        margin-left: 0;
    }

    .button--link {
        @include font-medium;
        color: $secondary-text-color;
        background-color: transparent;
        text-decoration: underline;
        padding: 0;
        margin: 0;
        opacity: 1;

        &:hover {
            color: rgba($secondary-text-color, .7);
            transition: $anim;
        }

    }

    .button--address-update {
        margin-right: gap(1);
    }
}

// -------------------------------------
// Order Page Styles
// -------------------------------------
.order-page__title {
    margin-top: 0;
    margin-bottom: gap(.5);
}

.order-page__subtitle {
    margin-bottom: gap(4);
}

.order-addresses {
    margin-bottom: 40px;
}

// -------------------------------------------------------------------
// Page Header Styles on Account related pages
// -------------------------------------------------------------------
.page-header--account-form {
    .page-header__content {
        padding-bottom: 0;
        text-align: center;
    }
}

.template-account {
    .page-header__title {
        margin-bottom: gap(3);
    }
}

.page-header--addresses {
    .page-header__content {
        padding-bottom: gap(3);
        
        @include bp(tablet) {
            padding-top: gap(5);
        }
    }

    .page-header__title {
        padding-top: gap(2);

        @include bp(tablet) {
            padding-top: gap(4);
        }
    }
}

.form__register-link {
    display: block;
    margin-bottom: gap(1);
}

// -----------------------------------------------
// Add custom styles to reCaptcha Challenge page
// -----------------------------------------------

.shopify-challenge__container {
    padding: gap(6) 0;

    .shopify-challenge__error {
        padding-left: 0;
        @include font-size(14);
        @include line-height(19);
        color: $error-color;
        margin-bottom: 20px;
    }

    .shopify-challenge__button {
        @include font-size(14);
        font-weight: bold;
        padding: 19px 25px 17px;
        text-align: center;
        text-transform: uppercase;
        background-color: $primary-color;
        color: $white;
        margin-top: 30px;
    }
}


// -----------------------------------------------------------
// Add custom styles to page section divider on Account Page
// -----------------------------------------------------------
.page-section__divider {
    width: 100%;
    margin: 0;
    height: 1px;
    background-color: $border-color--light;
    border: none;
}

.page-header--account-additional-info-submission {
    text-align: center;
}

.page-header--account {

    .page-header__content {
        padding: gap(3) 0;
    }
}

.page-header--account-page {
    margin-bottom: gap(6);

    .page-header__title {
        padding-top: gap(3);

        @include bp(laptop) {
            padding-top: gap(5);
        }
    }
}


// -----------------------------------------------------------
// Account Page - Customer Details
// -----------------------------------------------------------
.customer-details__row {
    display: flex;
    flex-flow: row nowrap;
    
    &:not(:last-of-type) {
        margin-bottom: 7px;
    }

    .customer-details__label {
        width: 38%;
        max-width: 200px;

        @include bp(tablet) {
            width: 200px;
        }
    }

    .customer-details__value {
        margin-left: 5px;
    }
}

#customer-edit__button {
    margin-top: gap(2);
}

#customer-edit__form {
    margin-top: gap(2);
    margin-bottom: gap(2);

    .form__title {
        margin-top: gap(3);
        margin-bottom: gap(2);
    }

    #create_customer {
        background-color: transparent;
        border-radius: 0;
        padding: 0;
        margin-left: 0;

        .cf-react-target {
            margin-left: 0.5em;
        }

        .cf-form-actions {
            justify-content: flex-start;
            
            .cf-submit-form {
                margin-left: 0;
            }
        }
    }
}


// -----------------------------------------------------------
// Register Page - Custom Styles for the Customers fields App
// -----------------------------------------------------------

.page-section__register {
    @include bp(laptop) {
        margin-top: gap(4);
    }
}

#create_customer {
    background-color: $light-grey-100;
    border-radius: 20px;
    padding: 40px;
    max-width: 1000px;
    margin: 0 auto;

    .cf-react-target {
        max-width: 100%;
        margin: 0 auto;
    }

    .cf-field {
        margin: 0 0 15px;
        padding: 0;

        &[data-cf-field-type='heading'] {
            margin-top: 0;
            margin-bottom: gap(2);

            & > * {
                margin: 0;
            }
        }

        &[data-cf-field-type='divider'] {
            margin-bottom: 0;
            margin-top: 5px;

            @include bp(tablet) {
                margin-left: -40px;
            }

            @include bp(laptop) {
                margin-left: -80px;
            }

            hr {
                background: #E0E0E0;

                @include bp(tablet) {
                    width: calc(100% + 40px);
                }
    
                @include bp(laptop) {
                    width: calc(100% + 80px);
                }
            }
        }

        label {
            margin-bottom: 5px
        }

        input,
        textarea,
        select {
            @include font-default;
            @include font-size(16);
            @include line-height(24);
            color: $primary-text-color;
            padding: gap(1.3) gap(1.5);
            background-color: $white;
            display: block;
            width: 100%;
            -webkit-appearance: none;
            -moz-appearance: none;
        
            &:focus {
                border-color: $primary-color;
            }
        
            &.error {
                border-color: $error-color;
            }
        }

        [type='checkbox'] {
            -webkit-appearance: none;
            -moz-appearance: checkbox;
            margin-right: gap(0.5);
            width: auto;
            background-color: transparent;
            padding: 0;
            width: 19px;
            height: 19px;

            &:before {
                content: "";
                position: relative;
                display: block;
                width: 19px;
                height: 19px;
                border: $border--light;
                background-color: $white;
            }
    
            &:after {
                content: "";
                background-repeat: no-repeat;
                background-position: center;
                position: relative;
                display: block;
                left: 2px;
                top: -16px;
                width: 15px;
                height: 12px;
            }
    
            &:checked:before {
                background-color: $primary-color;
                border-color:  transparent;
            }
    
            &:checked:after {
                background-repeat: no-repeat;
                background-position: center;
                background-image: url('data:image/svg+xml;base64,');
            }

            & + span {
                margin-left: 5px;
            }
        }

        &.newsletter-field {
            text-align: center;
            display: flex;
            justify-content: center;
            margin-top: 20px;
        }
    }

    .cf-field[data-cf-invalid=true]:not([data-cf-field-type=repeating_group]) {
        background: none;
        border-color: transparent;

        input,
        textarea,
        select {
            border-color: $error-color;
            background-color: $white;
            color: $primary-text-color;
        }
    }

    .cf-field[data-cf-invalid=true]:not([data-cf-field-type=repeating_group]) .cf-field-errors li {
        @include font-tags;
        @include font-size(16);
        @include line-height(24);
        color: $error-color;
    }

    .cf-field-description {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #484D60;
        padding: 0;
        opacity: 1;
    }

    .cf-step-invalid {
        width: 100%;

        .cf-error-message {
            width: 500px;
            border-color: $error-color;
        }

        .cf-error-message-header,
        .cf-field-errors b {
            @include font-tags;
            @include font-size(16);
            @include line-height(24);
            color: $error-color;
        }

    }

    .cf-form-actions {
        display: grid;
        justify-content: center;
        margin-top: 5px;

        .cf-cancel.cf-button{
            display: none;
        }
        .cf-submit-form {
            margin-bottom: 0;
        }
    }

    .cf-button {
        background-color:  transparent;
        color: $primary-color;
        border: 2px solid $primary-color;
        margin: auto;

        &:focus {
            color: $primary-color;
            outline: 2px solid $secondary-color;
            outline-offset: 0;
        }

        &:hover,
        &:active,
        a:hover & {
            background-color: $primary-color;
            color: $white;
        }

        &.button--disabled,
        &:disabled {
            background-color: $white;
            border: 2px solid rgba(200, 201, 207, .5);
            color:  rgba(#C8C9CF, .5);
            pointer-events: none;
        }
    }
}

#customer-edit__form {
    .cf-field {
        &[data-cf-field-type='heading'] {
            margin-top: gap(1);
            margin-bottom: gap(1.5);
        }
    }
}