// ----------------------------------------------------------------------------
// Search
// ----------------------------------------------------------------------------
//
// Contains styles for the two search forms (header, search page)
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$search-box-background-color: #C8C9CF !default;
$search-box-color: #787878 !default;
$search-box-height: 37px !default;
$search-box-button-width: 150px !default;
$search-box-transition: $anim !default;


// -------------------------------------
// Default Search
// -------------------------------------
.search-box--default {
    display: block;
    width: 100%;

    .search-box__toggle {
        display: none;
    }

    .form--search {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-around;
        position: relative;
        height: 37px;
    }

    .form__label {
        display: none;
    }

    .form__field {
        margin: 0;
    }

    .search__input,
    .search__submit {
        color: $search-box-color;
        border-radius: 0;
        height: $search-box-height;
        border: none;
    }

  
    .search__input {
        border-radius: 20px;
        padding-right: 50px;
        background-color: rgba($search-box-background-color, .3);
    }

    .search__submit {
        color: $primary-color;
        padding: 10px 17px;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
        position: absolute;
        right: 0;
        top: 0;
        background-color: transparent;
    }

    .form__field--search {
        // width: calc(100% - 50px);
        width: 100%;
        position: absolute;
    }

    .form__field--button {
        width: auto;
    }
}


// -------------------------------------
// Search -- Search Results Form
// -------------------------------------
.search-box--search-results {
    .form--search {
        border-bottom: 1px solid $primary-color;
        flex-flow: row nowrap;
        margin-bottom: 10px;
        width: 100%;

        @include bp(laptop) {
            max-width: calc(8/12 * 100%);
        }
    }

    .form__control {
        @include responsive-fontsize(22, 32, 32, 36);
        @include font-header;
    }

    .form__field {
        margin: 0;
    }

    .form__field--search {
        flex: 0 1 100%;
        width: 100%;
    }

    .form__field--button {
        flex: 0 1 auto;
        width: auto;
    }

    .search__input {
        border: 0;
        padding-left: 0;
        background-color: transparent;
    }

    .search__submit {
        @include font-size(24);
        background-color: transparent;
        padding-right: 0;

        @include bp(laptop) {
            @include font-size(32);
        }
    }
}


// -------------------------------------
// Search -- Header
// -------------------------------------
.search-box--header {
    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    padding: 0 0 10px;

    @include bp(laptop) {
        padding: 0;
    }

    .search-box__toggle {
        @include font-default;
        @include font-size(16);
        color: $primary-text-color;
        background: transparent;
        text-decoration: none;
        margin-right: 6px;
        display: flex;
        align-items: center;

        @include bp(laptop) {
            margin: 0;
            padding: 0;

            &:hover {
                color: $grey;
            }
        }

        .icon {
            width: 16px;
            vertical-align: initial;
        }
    }

    .search-box__toggle-title {
        margin-left: gap(.5);
    }

    .form--search {
        justify-content: flex-start;
        width: 100%;

        @include bp(laptop) {
            justify-content: flex-end;
            width: auto;
        }
    }

    .form__label {
        @include font-size(16);
        display: none;
        margin-bottom: 0;
    }

    .form__field--search {
        display: none;

        min-width: 200px;
        width: 100%;

        @include bp(laptop) {
            flex: 0 1 75%;
            min-width: 150px;
        }
    }

    .form__field--button {
        display: none;
        flex: 0 1 44px;
        padding: 0 10px;
    }

    .search__input {
        background-color: transparent;
        border: 0;
        border-bottom: $border--light;
        padding: 10px 0;
        border-radius: 0;
        appearance: none;

        &:focus {
            outline: none;
        }
    }

    .search__submit {
        @include font-size(18);
        background-color: transparent;
        padding: 10px 0;

        @include bp(laptop) {
            @include font-size(24);
        }
    }

    // When toggled, this changes:
    &.is-active {
        .form__label {
            display: none;
        }

        .form__field--search {
            display: inline-block;
        }

        .form__field--button {
            display: inline-block;
        }

        .search-box__toggle {
            display: none;
        }
    }
}

// -------------------------------------
// Search -- Smart Search & Filter
// -------------------------------------
.snize-title {
    @include font-header;
}

.snize-ac-results {
    // Labels & Heading
    .snize-ac-results-content {
        .snize-label {
            color: $primary-color;
            font-family: $font--header;
            font-style: normal;
            font-weight: 800;
            margin-top: gap(2);

            @include bp(laptop) {
                @include font-size(16);
            }
        } 
        
        // tags
        .snize-product-tag-label {
            color: $primary-color;
            margin-left: 10px;
            margin-top: 5px;
        }
    }

    // Pages & Catergories
    .snize-ac-results-column,
    .snize-ac-results-content,
    .snize-ac-results-singlecolumn-list {
        .snize-ac-results-list {
            .snize-view-link {
                color: $primary-color;
                @include responsive-fontsize(16, 24, 16, 24);
                text-decoration: underline;
                text-underline-position: under;
                text-decoration-color: transparent;
            }

            .snize-category,
            .snize-page {
                // on hover
                &.snize-ac-over-nodrop,
                &.snize-ac-over-nodrop-mouse {
                    background: transparent;

                    .snize-view-link {
                        color: $primary-text-color;
                        text-decoration: underline;
                        text-decoration-color: $secondary-color;
                        transition: $anim;
                    }
                }
            }
        }

        .snize-page {
            .snize-view-link {
                color: $primary-color;
                @include responsive-fontsize(16, 24, 16, 24);
                text-decoration: underline;
                text-underline-position: under;
                text-decoration-color: transparent;
            }

            // on hover
            &.snize-ac-over-nodrop,
            &.snize-ac-over-nodrop-mouse {
                background: transparent;

                .snize-view-link {
                    color: $primary-text-color;
                    text-decoration: underline;
                    text-decoration-color: $secondary-color;
                    transition: $anim;
                }
            }
        }

        .snize-view-all-categories,
        .snize-view-all-pages,
        .snize-view-all-products {
            text-underline-position: under;
            @include font-header;
            @include font-size(16);
            @include line-height(24);

