                background-position: center;
                background-size: 100%;
                width: 18px;
                height: 11px;
            }

            &:hover {
                text-decoration: underline;
                text-decoration-color: $secondary-color;
                
                .snize-ac-results-arrow {
                    background-image: url("data:image/svg+xml,%3Csvg width='18' height='11' viewBox='0 0 18 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.1815 1.00052C12.0762 1.00554 11.9748 1.04205 11.8901 1.10542C11.8054 1.16879 11.7411 1.25618 11.7055 1.35652C11.6698 1.45686 11.6644 1.56564 11.6899 1.66908C11.7154 1.77253 11.7707 1.866 11.8487 1.93765L15.0987 4.95669H1.56086C1.54239 4.95579 1.52387 4.95579 1.5054 4.95669C1.2266 4.97132 0.985123 5.2275 1.00072 5.52346C1.01629 5.81906 1.28206 6.04886 1.56086 6.03411C1.83967 6.01937 15.0987 6.03411 15.0987 6.03411L11.8487 9.04754C11.7933 9.0943 11.7479 9.15197 11.7152 9.21704C11.6825 9.2821 11.6632 9.35322 11.6585 9.42606C11.6538 9.4989 11.6638 9.57195 11.6878 9.64078C11.7119 9.7096 11.7495 9.77277 11.7984 9.82644C11.8473 9.8801 11.9065 9.92316 11.9723 9.95298C12.0381 9.98281 12.1093 9.99878 12.1814 9.99993C12.2536 10.0011 12.3252 9.9874 12.3919 9.95969C12.4587 9.93199 12.5192 9.89085 12.5697 9.83877L16.8291 5.88821C16.883 5.83777 16.926 5.77658 16.9555 5.7085C16.9849 5.64041 17.0001 5.56691 17.0001 5.49259C17.0001 5.41828 16.9849 5.34477 16.9555 5.27669C16.926 5.20861 16.883 5.14742 16.8291 5.09698L12.5697 1.14642C12.5179 1.09709 12.4569 1.05877 12.3902 1.03371C12.3235 1.00865 12.2526 0.997366 12.1815 1.00052Z' fill='%2300E3E6' stroke='%2300E3E6'/%3E%3C/svg%3E");
                }
            }
        }
    }

    // Suggestion
    .snize-ac-results-content {
        .snize-suggestion {
            // on hover
            &.snize-ac-over-nodrop,
            &.snize-ac-over-nodrop-mouse {
                background: transparent;

                &:before {
                    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.6 3.75C6.76295 3.75 3.75 6.77915 3.75 10.5409C3.75 14.3001 6.76045 17.3318 10.5 17.3318C11.9854 17.3318 13.3777 16.8561 14.5821 15.9089L18.6227 19.975C18.7663 20.1195 19.0288 20.25 19.3 20.25C19.5712 20.25 19.8337 20.1195 19.9773 19.975C20.1209 19.8305 20.25 19.567 20.25 19.2956C20.25 19.185 20.2027 19.0662 20.1614 18.9831C20.1162 18.8922 20.052 18.792 19.9773 18.7169L15.9348 14.6489C16.8771 13.4366 17.35 12.0355 17.35 10.5409C17.35 6.78165 14.3395 3.75 10.6 3.75ZM5.65 10.5409C5.65 7.85986 7.83955 5.65881 10.5 5.65881C13.1605 5.65881 15.35 7.85986 15.35 10.5409C15.35 13.3225 13.2605 15.423 10.5 15.423C7.83955 15.423 5.65 13.2219 5.65 10.5409Z' fill='%2300E3E6'/%3E%3C/svg%3E%0A");
                }
            }
        }
    }

    .snize-ac-results-column,
    .snize-ac-results-list {
        .snize-suggestion {
            text-decoration: underline;
            text-underline-position: under;
            text-decoration-color: transparent;

            // on hover
            &.snize-ac-over-nodrop,
            &.snize-ac-over-nodrop-mouse {
                background: transparent;
                color: $primary-text-color;
                text-decoration: underline;
                text-decoration-color: $secondary-color;
                transition: $anim;
            }
        }
    }

    // Products
    .snize-ac-results-multicolumn-list,
    .snize-ac-results-singlecolumn-list {
        .snize-product {
            .snize-title {
                @include font-header;
                @include responsive-fontsize(16, 24, 16, 24);
                color: $primary-color;
                text-decoration: none;
                max-height: 100%;
            }

            .snize-description {
                display: none;
            }

            .snize-price-list {
                &,
                .snize-price {
                    font-weight: 400;
                    color: $primary-color;
                    @include responsive-fontsize(14, 17, 14, 17);
                }

                .snize-price {
                    margin-top: 5px;
                    display: block;
                }
            }

            // on hover
            &.snize-ac-over-nodrop,
            &.snize-ac-over-nodrop-mouse {
                background: transparent;
            }
        }
    }

    .snize-ac-results-multicolumn-list {
        .snize-product {
            .snize-thumbnail {
                max-height: 128px;
                height: 100%;
                border: 1px solid rgba(200, 201, 207, 0.5);
                border-radius: 4px;
                overflow: hidden;

                img {
                    max-height: 100%;
                }
            }

            @include bp(laptop) {
                &:hover {
                    .snize-thumbnail {
                        border: 2px solid $cyan;
                        transition: $anim;
                    }
                }
            }
        }
    }

    .snize-ac-results-list {
        .snize-product {
            .snize-description {
                font-weight: 400;
                color: $primary-color;
                @include responsive-fontsize(14, 17, 14, 17);
                margin-top: 5px;
                margin-bottom: 0;
            }

            .snize-thumbnail {
                height: 100%;
                border: 1px solid rgba(200, 201, 207, 0.5);
                border-radius: 4px;
                overflow: hidden;

                img {
                    max-height: 100%;
                    max-width: 100%;
                }
            }

            &::after {
                border-top: none;
            }

            // on hover
            &.snize-ac-over-nodrop,
            &.snize-ac-over-nodrop-mouse {
                background: transparent;
            }

            @include bp(laptop) {
                &:hover {
                    .snize-thumbnail {
                        border: 2px solid $cyan;
                        transition: $anim;
                    }
                }
            }
        }
    }
}