// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// ----------------------------------------------------------------------------
// Product Tags
// ----------------------------------------------------------------------------
.product-tags {
    position: relative;
}

.product-tags {

    &.product-tags--badge {
        position: absolute;
        z-index: 2;
        border-radius: 20px;
        background-color: $cyan;
        padding: 0 gap(0.5);
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 5px;
        padding-bottom: 5px;

        .product-tags__label {
            line-height: 1;
        }

        @include bp(tablet) {
            padding: gap(0.5) gap(1.5);
        }

        span {
            @include font-default;
            @include font-size(12);
            font-weight: bold;

            @include bp(laptop) {
                @include font-size(11);
            }

            @include bp(desktop) {
                @include font-size(12);
            }
        }

        &.product-tags--badge--yellow {
            background-color: $yellow;
            color: $black;
        }

        &.product-tags--badge--cyan {
            background-color: $cyan;
            color: $primary-color;  
        }

        &.product-tags--badge--blue {
            background-color: $bright-blue;
            color: $white;
        }
    }
}
