// ----------------------------------------------------------------------------
//  Intro Block
// ----------------------------------------------------------------------------
//
// Minor component that may include a logo or link, includes a title and content
// - two styles, one with a logo the other without
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$intro-block-background-color: $grey;


// -------------------------------------
// Section Intro - Default
// -------------------------------------

.intro-block__title {
    margin-bottom: 0;
}


// -------------------------------------
// Section Intro - Brand/Default
// -------------------------------------

.page-section--intro-block{
    background-color: $intro-block-background-color;
    padding: gap(2) 0;
   
    @include bp(laptop) {
        position: relative;
        padding: 0;
        z-index: 1;
    }

    &.page-section{
        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &::before,
    &::after{
        content: '';
        background-image: url("data:image/svg+xml,%3Csvg width='269' height='230' viewBox='0 0 269 230' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M306.122 230C302.008 230 297.835 229.516 293.722 228.488L40.7169 167.298C16.3941 161.433 0 140.391 0 114.996C0 89.6002 16.3344 68.6188 40.7169 62.6932L293.722 1.50263C309.579 -2.36713 326.033 1.32125 338.85 11.6003C351.667 21.8794 359 37.2375 359 53.8049V176.126C359 192.693 351.667 208.051 338.85 218.33C329.371 226.009 317.925 230 306.122 230ZM306.062 24.9026C303.856 24.9026 301.591 25.1445 299.385 25.6886L46.3803 86.8793C30.2247 90.8095 24.4421 104.475 24.4421 115.056C24.4421 125.577 30.1651 139.302 46.3803 143.233L299.385 204.423C307.91 206.479 316.793 204.484 323.708 198.981C330.623 193.479 334.558 185.195 334.558 176.247V53.8654C334.558 44.977 330.623 36.6933 323.708 31.1305C318.641 27.0794 312.441 24.9026 306.062 24.9026Z' fill='%2300E3E6'/%3E%3C/svg%3E%0A");
        background-size: contain;
        background-repeat: no-repeat;
        background-position-x: right;
        height: 150px;
        width: 100%;
        display: inline-block;
        
        @include bp(laptop) {
            position: absolute;
            height: 230px;
            z-index: -1;
        }
    }

    &::before{
        @include bp(laptop) {
            top: 20px;
        }
    }

    &::after{
        transform: rotate(180deg);
        @include bp(laptop) {
            bottom: 20px;
        }
    }
}

.intro-block {

    @include bp(laptop) {
        padding: 122px 0;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
        align-items: center;
    }

    * {
        color: $white;
    }

    .intro-block__image {
        max-width: 360px;
        padding: 0 gap(6);
        margin: 0 auto;
        flex: 0 1 40%;

        + .intro-block__content {
            @include bp(laptop) {
                text-align: left;
            }
        }

        img {
            width: 100%;
        }
    }

    .intro-block__content {
        text-align: center;
        margin: 0 14px;

        @include bp(laptop) {
            flex: 0 1 65%;
            max-width: 905px;
            margin: 0;
        }
    }

    .copy-block__h3{
        margin: 11px 0;
    }

    .intro-block__body {
        margin: gap(1) 0 0;
    }

    .button {
        margin: 21px 0 0 0;

        @include bp(laptop) {
            flex: 0 1 16.666%;
        }
    }

    .intro-block__icon{
        &.icon{
            height: 110px;
            width: auto;
            @include bpgd(laptop){
                margin-bottom: 19px;
            }
        }
    }
}
