// ----------------------------------------------------------------------------
// Testimonial Slider
// ----------------------------------------------------------------------------
//


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$carousel-arrow-background: $white;
$carousel-arrow-border-radius: 5px !default;
$carousel-arrow-color: $primary-color;
$carousel-arrow-box-shadow: 0px 4.008px 20.04px 2.004px rgba(0, 0, 0, 0.05);
$carousel-arrow-padding: 0;
$carousel-container-background-color--blue: rgba(45, 74, 249, 0.2);
$carousel-container-background-color--coral: rgba(242, 88, 81, 0.5);


// -------------------------------------
// Testimonial Slider - Section
// -------------------------------------
.page-section--testimonial-slider {

    .container {

        @include bpgd(laptop) {
            width: 100%;
        }
    }
}

.page-section__header--testimonial-slider {
    margin-left: $grid-gutter--mobile;
    margin-bottom: gap(3);
    text-align: left;

    @include bp(laptop) {
        margin-left: 0;
        text-align: center;
    }

    .page-section__title {
        margin-top: 0;
        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: 0;
        }
    }
}



// -------------------------------------
// Testimonial Slider - Carousel
// -------------------------------------
.carousel--testimonial-slider {
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;

        @include bp(laptop) {
            width: calc(75% - 50px);
            left: 60px;
        }
    }

    .content-panel--default {

        .content-panel__container {
            display: block;

            @include bp(laptop) {
                display: flex;
                align-items: center;
                padding: gap(5) gap(6) gap(2) gap(9);
            }

            .content-panel__meta {
                margin: gap(3.5) gap(3.5) gap(3);
                height: auto;
                border-radius: 10px;
                background-color: transparent;

                @include bp(laptop) {
                    margin: 0 0 0 gap(2);
                }

                .background-image {
                    @include aspect-ratio(1, 1);
                    background-color: transparent;
                }
            }

            .content-panel__content {
                display: flex;
                flex-flow: row wrap;
                align-items: center;
                min-height: 340px;
                padding: gap(3);
                margin: 0 gap(1.5) gap(2.5);
                background-color: $background-color--dark;
                border-radius: 20px;

                @include bp(laptop) {

                    padding: gap(6);
                    margin: 0 0 0 gap(6);
                }

                .copy-block__text,
                .testimonials__author-info {
                    color: $white;
                }

                .copy-block__text {
                    margin-top: gap(3);

                    a {
                        color: $white;
                    }
                }

                .testimonials__author {
                    margin-top: gap(3);
                }
            }
        }
    }


    .swiper-wrapper {
        position: relative;
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    .swiper-arrow {
        background: $carousel-arrow-background;
        border: 1px solid $border-color--light;
        border-radius: $carousel-arrow-border-radius;
        box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
        color: $carousel-arrow-color;
        padding: $carousel-arrow-padding;
        height: 51px;
        width: 35px;

        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
            box-shadow: $carousel-arrow-box-shadow;
            border-color: $cyan;
        }

        &.swiper-button-next {
            right: 0;

            @include bp(laptop) {
                right: gap(1);
            }

            &::after {
                content: "";
            }
        }

        &.swiper-button-prev {
            border-radius: $carousel-arrow-border-radius;
            left: 0;

            @include bp(laptop) {
                left: gap(1);
            }

            &::after {
                content: "";
            }

            .icon {
                margin-right: 1px;
            }
        }

        &.swiper-button-disabled {
            display: none;
        }

        svg {
            height: 15px;
            width: 8px;
        }
    }

    .swiper-pagination.swiper-pagination-bullets {
        position: relative;
        margin-bottom: gap(2);

        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            background-color: $grey;
            opacity: 1;

            &:not(:last-child) {
                margin-right: gap(.3);
            }
        }

        .swiper-pagination-bullet-active {
            background-color: $secondary-color;
        }
    }

    .slider__icon {

        svg {
            width: 40px;
            height: 40px;
        }
    }

    &.carousel--testimonial-slider--blue {

        &::after {
            background-color: $carousel-container-background-color--blue;
        }

        .content-panel--default .content-panel__container .content-panel__content {

            .slider__icon {
                color: $bright-blue;
            }
        }
    }

    &.carousel--testimonial-slider--coral {

        &::after {
            background-color: $carousel-container-background-color--coral;
        }

        .slider__icon {
            color: $coral;
        }
    }
}
