// ----------------------------------------------------------------------------
// HTML Tags
// ----------------------------------------------------------------------------
//
// Styles for the base HTML tags
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// HTML Tag Styles
// -------------------------------------

html {
    font-size: 62.5%;
}

body {
    @include font-primary;
    font-size: 1.6rem;
}

body,
input,
textarea,
button,
select {
    -webkit-font-smoothing: antialiased;
    text-size-adjust: 100%;
}

p {
    margin: 0;
    margin-bottom: gap(2);

    &:last-of-type {
        margin-bottom: 0;
    }
}

a {
    @include font-medium;
    color: $primary-text-color;
    text-decoration: underline;
    text-decoration-color: $secondary-color;
    text-underline-position: under;

    &:hover {
        color: $primary-color;
        transition: $anim;
        text-decoration-color: $primary-color;
    }

    &:focus {
        color: inherit;
    }
}

small {
    @include font-caption;
}

// Lists
ul,
ol,
dl {
    @include font-primary;
}



// -------------------------------------
// Headings
// -------------------------------------

@include headings(1, 4) {
    @include font-header;
    color: $black;
}

@include headings(5, 6) {
    @include font-default;
    color: $black;
}

h1,
.h1 {
    @include responsive-fontsize(45, 45, 60, 60);
}

h2,
.h2 {
    @include responsive-fontsize(40, 40, 52, 66);
}

h3,
.h3 {
    @include responsive-fontsize(32, 40, 48, 59);
}

h4,
.h4 {
    @include responsive-fontsize(24, 32, 36, 44);
}

h5,
.h5 {
     @include responsive-fontsize(18, 28, 24, 32);
     font-weight: 800;
}

h6,
.h6 {
    @include font-size(16);
}


// -------------------------------------
// Additional Custom Classes
// -------------------------------------

// Example
.text--caption {
    @include font-caption;
}

// Subtitle text
.subtitle {
    @include font-header;
    @include responsive-fontsize(14, 20, 18, 20);
    text-transform: uppercase;
    letter-spacing: 3px;

    &.subtitle--blue {
        color: $bright-blue;
    }

    &.subtitle--coral {
        color: $coral;
    }
}
