// ----------------------------------------------------------------------------
// Pages
// ----------------------------------------------------------------------------
//
// Add generic page template styles here.
// To be used for page level styles overrides, not section/snippet level styles.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


.page-section--page {
    .container {
        width: 100%;
        max-width: 100%;

        @include bp(laptop) {
            @include container--wysiwyg;
            margin: 0;
        }

        h2:first-of-type,
        h3:first-of-type {
            margin-top: 0;
        }
    }
}

// ----------------------------
// Shopify Policies Page
// ----------------------------
.shopify-policy__container {
    padding-top: gap(4);
    padding-bottom: gap(4);

    @include bp(tablet) {
        padding-top: gap(6);
        padding-bottom: gap(6);
    }

    h1 {
        margin-top: 0;
        text-align: left;
    }
}


/* gm css */
.gm-submenu {
    border: 1px solid #c8c9cf;
    border-top: none;
    box-shadow: 0 5px 5px -5px #c8c9cf,
                -5px 0 5px -5px #c8c9cf, 
                5px 0 5px -5px #c8c9cf;
}
  
  
/* globo sw css */

@media (min-width: 1024px) {
    .product .product__images {
        position: sticky;
        top: 20%;
    }
}