// ----------------------------------------------------------------------------
// Tabs Section
// ----------------------------------------------------------------------------
//
// Mobile - Collapsible tab block used to reveal extra product information when the
// user clicks an item's header or expand button.
//
// Desktop - Tabs
//
// Related JS file: tabs/tabs.js
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

// for default tabs
$tab-transition: $anim !default;
$tab-question-mobile-padding: gap(1) $grid-gutter--mobile !default;
$tab-answer-background-color: transparent !default;
$tab-content-padding: 0 !default;
$tab-default-text-btn-color: $white;
$tab-default-background-color: $primary-color;
$tab-default-border: 1px solid #C8C9CF;

// for active tab
$tab-active-content-padding: gap(2) $grid-gutter--mobile !default;
$tab-active-text-btn-color: $primary-color;
$tab-active-background-color: rgba(0, 227, 230, 0.3);

// -------------------------------------
// Accordion Grid -- Tabs Styles
// -------------------------------------
.page-section--tabs {
    grid-column: full;

    .container {
        @include bpgd (tablet) {
            width: 100%;
        }
    }

    .css-grid--accordion {
        @include bpgd (tablet) {
            grid-template-columns: repeat(1,1fr) !important;
        }
    }

    .css-grid__item--panel {
        overflow: hidden;
        &.js-active {
            border-bottom: none;

            @include bp (laptop) {
                border-bottom: $tab-default-border;
            }
        }
    }
}

.tabs {
    width: 100%;

    @include bp(laptop) {
        text-align: center;
    }

    .button {
        margin-top: gap(3);

        @include bp(laptop) {
            margin-top: 0;
        }
    }
}

.tab__title {
    @include responsive-fontsize(18, 28, 22, 24);
    @include font-header;
    color: $tab-default-text-btn-color;
    background-color: $tab-default-background-color;
    width: 100%;
    padding: $tab-question-mobile-padding;
    margin: 0;
    transition: $tab-transition;
    text-decoration: none;
    border-radius: 0;

    @include bp(laptop) {
        height: 100%;
        display: flex;
        justify-content: center;
        flex-direction: row;
        padding: gap(2);
        text-align: center;

        &::after{
            opacity: 0;
            display: block;
            content: '';
            width: 0; 
            height: 0; 
            border-left: gap(2) solid transparent;
            border-right: gap(2) solid transparent;
            border-top: gap(2) solid $tab-active-background-color;
            position: absolute;
            left: 50%;
            bottom: gap(-2);
            transform: translate(-50%, 0px);
            transition: $tab-transition;
        }

        .js-active & {
            opacity: 1;

            &::after{
                opacity: 1;
                transition: $tab-transition;
            }
        }
    }

    .js-active & {
        transition: $tab-transition;
        background-color: $tab-active-background-color;
        color: $tab-active-text-btn-color;
    }

    .ie11 & {
        @include bp(laptop) {
            text-align: left;
        }
    }
}

.css-grid--accordion {
    .css-grid__item--card {
        border-bottom: none;
        transition: $tab-transition;

        @include bp(laptop) {
            border-radius: 0;
            white-space: normal;
            align-items: flex-start;
            cursor: pointer;
            position: relative;
            color: $tab-default-text-btn-color;

            &:first-of-type {
                .tab__title{
                    border-radius: gap(2) 0px 0px 0px;
                }
            }

            &:last-of-type {
                .tab__title{
                    border-radius: 0px gap(2) 0px 0px;
                }
            }

            &:only-of-type {
                .tab__title{
                    border-radius: gap(2) gap(2) 0px 0px;
                }
            }
        }

        &:hover {
            transition: $tab-transition;

            &,
            .tab__title {
                color: $tab-default-text-btn-color;

                @include bp(laptop) {
                    text-decoration: underline;
                }
            }
        }

        &.js-active {
            @include bp(laptop) {
                border-bottom: none;
            }

            &:hover {
                &,
                .tab__title {
                    color: $tab-active-text-btn-color;
                }
            }
        }
    }
}

.tab-content__wrapper {
    @include clearfix;
    background-color: $tab-answer-background-color;
    padding: $tab-content-padding;
    text-align: left;
    transition: $tab-transition;

    .js-active & {
        padding: $tab-active-content-padding;
        transition: $tab-transition;

        @include bp(laptop) {
            padding: gap(5) gap(6);
        }
    }

    p:last-child {
        margin-bottom: 0;
    }
}

.css-grid--accordion {
    .css-grid__item--panel {
        padding: 0;

        &:last-of-type {
            border-bottom: 1px solid $border-color--light;
        }

        @include bp (laptop) {
            border-radius: 0px 0px 20px 20px;
            border: $tab-default-border;
            border-top: 0;
        }
    }
}

.tab__icon {
    margin: auto 0;
    display: inline-block;
    float: right;
    pointer-events: none;
    transition: $tab-transition;

    @include bp(laptop) {
        display: none;
    }

    .icon__minus {
        width: 15px;
        height: 15px;
    }

    .icon__plus {
        width: 16px;
        height: 16px;
    }

    .tab__icon--close {
        display: none;
    }

    .tab__icon--open {
        display: block;
    }

    .js-active & {
        .tab__icon--close {
            display: block;
        }

        .tab__icon--open {
            display: none;
        }
    }

    .ie11 & {
        @include bp(laptop) {
            display: inline-block;
        }
    }
}

// -------------------------------------
// Tabs Styles -- Resources
// -------------------------------------

.resource-tabs--heading {
    margin: 0;
}

.resource-tabs--subtitle {
    margin-bottom: gap(1);
    display: block;
}

.resource-tabs--description {
    margin-top: gap(1);
}

.resource-tabs--header-wrapper {
    max-width: 846px;
    padding: $grid-gutter--mobile;
    margin-left: 0;

    @include bp(laptop) {
        padding: 0;
    }
}

.tabs--resources {
    margin-top: gap(4);
    .tab-content__wrapper {
        display: grid;
        grid-gap: gap(0.7);
        padding: $grid-gutter--mobile;
        padding-bottom: 0;

        @include bp(laptop) {
            grid-gap: gap(3);
            padding: gap(5) gap(3);
            padding-bottom: gap(2);
        }
    }

    .card__resource-footer {
        .button {
            margin-top: 0;
        }
    }
}
