// ----------------------------------------------------------------------------
// Card -- Default
// ----------------------------------------------------------------------------
//
// A card component, which can be integrated as a collection within the css-grid
// or flex-grid components to create content drivers.
//
// Default card styles match most closely with a wireframeing example of cards,
// with an image, title, description, and button inheriting core styles.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$card-background-color: transparent !default;
$card-border: 1px solid rgba(200, 201, 207, 0.5);
$card-content-padding: $grid-gutter !default;
$card-box-shadow: 0px 5px 20px rgba(0, 19, 57, 0.05);
$card-tag-background-color: $secondary-color;

// -------------------------------------
// Card -- Base
// -------------------------------------
// There are no base styles to override;
// allows for easier variant styling.
.card {
    width: 100%;
}

.card__feature.background-image {
    @include aspect-ratio(1, 1);
}

.card__wrapper {
    display: block;
    height: 100%;
}

// -------------------------------------
// Card -- Default
// -------------------------------------
.card--default {
    position: relative;
    background-color: $card-background-color;
    border: none;
    overflow: visible;

    a {
        text-decoration: none;
    }

    .card__content {
        padding: 0;
    }

    .card__title {
        @include font-header;
        @include font-size(24);
        @include line-height(32);
        margin: gap(2) 0 gap(1);
        color: $primary-color;
    }

    .card__link {
        text-decoration: none;
        margin-top: gap(2.5);
    }

    .card__tags-list {
        margin: 0 0 gap(1);
        padding: 0;
        list-style-type: none;

        li {
            display: inline-block;

            & + li {
                margin-left: gap(1);
            }
        }

        a {
            text-decoration: underline;
        }
    }

    .card__copy {
        @include font-default;
        @include font-size(16);
        @include line-height(24);
        color: $primary-text-color;
    }

    .card__button {
        @include font-primary;
        @include font-header;
        font-weight: 700;
        display: flex;
        align-items: center;
        opacity: 0;
        position: absolute;
        bottom: 20px;
        right: 20px;
        background-color: $white;
        color: $navy;
        z-index: 4;
        box-shadow: 0px 4px 20px 2px rgba(0, 0, 0, 0.05);
        border-radius: 20px;
        padding: gap(.3) gap(2);
        padding-top: gap(.5);

        .icon {
            margin-left: 3px;
            width: 16px;
        }
    }

    .card__tag {
        @include font-tags;
        padding: 0 gap(1);
        border-radius: 17px;
        position: absolute;
        top: -12px;
        left: 10px;
        z-index: 3;
        color: $white;
        background-color: $card-tag-background-color;
        transition: transform 400ms ease;
    }

    .card__image-wrapper {
       //@include aspect-ratio(4,3);
        height: 300px;
        margin: 0;
        display: block;
        position: relative;
        transform: translate(0);
        transition: box-shadow 400ms ease, transform 400ms ease;
        border-radius: 2px;
        overflow: hidden;

        @include bp(laptop) {
            border-radius: 3px;
        }
    }

    @include bp(laptop) {
        &:hover {
            .card__button {
                box-shadow: 0px 4px 20px 2px rgba(0, 0, 0, 0.05);
            }
       }
   }
}

// -------------------------------------
// Card -- Post
// -------------------------------------
.card--post {
    .card__title {
        @include font-size(20);
        @include line-height(24);
    }

    .card__image-wrapper {
        @include aspect-ratio(3, 2);
        height: auto;
        position: relative;
        border-radius: 0;

        .background-image {
            position: absolute;
        }
    }

    @include bp(laptop) {
         &:hover {
            .card__image-wrapper {
                transform: translateY(-10px);
                box-shadow: $card-box-shadow;
            }

            .card__title {
                color: $primary-color;
            }
        }
    }
}

// -------------------------------------
// Card -- Product
// -------------------------------------
.card--product {
    & > a {
        display: block;
        position: relative;
        z-index: 5;
    }

    .card__image-wrapper {
        border-radius: 4px;
        height: auto;
        position: relative;
        padding-bottom: calc(100% - 2px); // minus the border width
        border: $card-border;

        .background-image {
            -webkit-backface-visibility: hidden;
            transform: translate(0);
            transition: opacity 200ms ease;
            object-fit: contain;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            box-shadow: $card-box-shadow;
        }
    }

    .card__tag,
    .product-tags.product-tags--badge {
        z-index: 6;
        top: -10px;
        left: 15px;
        transition: transform .4s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 5px;
        padding-bottom: 5px;

        .product-tags__label {
            line-height: 1;
        }
    }

    .card__content {
        display: block;
        padding: gap(1) gap(.5);
        text-decoration: none;
        color: $primary-text-color;

        @include bp(laptop) {
            padding: gap(1) gap(0) 0;
        }
    }

    .card__subtitle {
        @include font-size(16);
        @include line-height(26);
        font-weight: 400;
        margin: 0;
        text-decoration: none;
        margin-top: gap(1);
    }

    .card__title {
        @include responsive-fontsize(16, 24, 16, 24);
        margin-top: gap(0.5);
        text-decoration: none;
    }

    .card__price {
        @include font-default;
        @include font-size(16);
        @include line-height(26);
        font-weight: 400;
        text-decoration: none;
        margin-top: gap(0.9);
    }

    .card__price--sale {
        color: $grey;

        & + .card__price--original {
            color: $sale-color;
            padding-left: gap(.5);
        }
    }

    @include bp(laptop) {
        // Card Hover styles
        &:hover {
            .card__image-wrapper {
                transform: translateY(-10px);
                box-shadow: $card-box-shadow;
                padding-bottom: calc(100% - 4px); // minus the border width
                border: 2px solid $cyan;
            }

            .product-tags.product-tags--badge {
                transform: translateY(-10px);
            }

            .card__title {
                color: $primary-color;
            }

            .card__button {
                opacity: 1;
                border: 1px solid $light-grey;
            }

            .card__title {
                color: $primary-color;
            }

            .card__tag {
                transform: translateY(-10px);
            }
        }
    }
}

// -------------------------------------
// Card -- Collection
// -------------------------------------
.card--collection {
    .card__image-wrapper {
        height: 110px;
        border: $card-border;
        border-radius: 4px;

        @include bp(laptop) {
            height: 195px;
        }
    }

    .card__content {
        display: block;
        padding: gap(1) gap(.5);
        text-decoration: none;
        color: $primary-text-color;

        @include bp(laptop) {
            padding: gap(1.5) gap(0);
        }
    }

    .card__title {
        margin: 0;
        text-decoration: none;
        padding-top: gap(1);

        @include responsive-fontsize(16, 24, 18, 24);

        @include bp(laptop) {
            margin-top: gap(0.5);
        }
    }

    @include bp(laptop) {
        // Card Hover styles
        &:hover {            
            .card__image-wrapper {
                transform: translateY(-10px);
                box-shadow: $card-box-shadow;
                border: 2px solid $cyan;
            }

            .card__title {
                color: $primary-color;
            }

            .card__button {
                opacity: 1;
                border: 1px solid $light-grey;
            }
        }
    }
}

// ---------------------------------------------
// Card -- Product add to cart confirmation item
// ---------------------------------------------
.card--line-item {
    display: flex;
    grid-gap: 15px;
    border: 0;

    @supports (display: grid) {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .ie11 & {
        a {
            width: 50%;
            margin-right: 10px;
        }
    }

    .card__title {
        @include font-size(18);
        @include line-height(23);
        margin: 0 0 gap(1);
    }

    .line-item__variant-title,
    .line-item__variant-sku {
        @include font-size(16);
        margin: 0;
        font-weight: 400;
    }

    .line-item__added-quantity {
        @include font-size(16);
        margin: 0 0 gap(.5);
        font-weight: 400;
    }

    .card__price {
        @include font-size(16);
        font-weight: 400;
    }

    .card__price--sale {
        @include font-size(16);
        color: $grey;

        & + .card__price--original {
            color: $red;
            padding-left: gap(.5);
        }
    }

    a {
        text-decoration: none;
        color: $primary-text-color;
    }
}


// -------------------------------------
// Card -- Event
// -------------------------------------
.card--event {
    display: flex;
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid transparent;
    transition: all 0.25s ease-in-out;

    .card__event-wrapper {
        display: flex;
        flex-direction: column;
        border: 1px solid rgba(200, 201, 207, 0.5);
        transition: all 0.25s ease-in-out;
        border-radius: 10px;
        position: relative;
    }

    .card__title {
        @include font-header;
        @include font-size(14);
        @include line-height(24);
        color: $primary-color;
        margin-top: gap(1);
    }

    .card__image-wrapper {
        @include aspect-ratio(3, 2);
        border-radius: 0;
        height: auto;

        .background-image {
            position: absolute;
        }
    }

    .card__link-wrapper {
        font-weight: 400;
    }

    .card__content {
        justify-self: stretch;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        padding: gap(2);
    }

    .card__event-date {
        margin-bottom: gap(1);
        @include font-size(14);
        @include line-height(24);
    }

    .card__copy {
        margin-bottom: gap(1);
        @include font-size(15);
        @include line-height(24);
    }

    .card__event-credit {
        margin-bottom: gap(1);
        @include font-size(14);
        @include line-height(24);
    }

    .card__event-type {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        text-transform: uppercase;
        color: $primary-color;
        font-weight: 800;
        @include font-size(14);
        @include line-height(24);

        .icon {
            @include font-size(25);
            margin-right: gap(.5);
        }
    }

    .card__event-footer {
        @include line-height(38);
        vertical-align: middle;
        border-top: 1px solid $border-color--light;
        padding-top: gap(2);

        .card__link-wrapper {
            @include font-size(14);
            @include line-height(24);
        }
    }

    .card__event-price {
        display: inline-block;
    }

    .card__link {
        float: right;
        margin-top: 0;
        padding: gap(.5) gap(1.5);
        position: relative;
        z-index: 2;
    }

    .card__link--overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }

    @include bp(laptop) {
        &:hover {
            box-shadow: $card-box-shadow;
            border: 1px solid $border-color;
            transition: all 0.25s ease-in-out;

            .card__event-wrapper{
                border: 1px solid $border-color;
                transition: all 0.25s ease-in-out;
            }

            .card__title {
                color: $primary-color;
            }
        }
    }
}

// -------------------------------------
// Card -- Resource
// -------------------------------------
.card--resource {
    position: relative;
    display: flex;
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid transparent;
    transition: all 0.25s ease-in-out;
    margin-bottom: gap(2.3);

    @include bp(laptop) {
        margin-bottom: 0;
    }

    .card__resource-wrapper {
        display: flex;
        flex-direction: column;
        border: 1px solid rgba(200, 201, 207, 0.5);
        transition: all 0.25s ease-in-out;
        border-radius: 10px;
        width: 100%;
    }

    .card__title {
        @include font-primary;
        @include responsive-fontsize(14, 15, 16, 24);
        color: $primary-color;
        margin: 0;
        font-weight: 800;
    }

    .card__image-wrapper {
        border-radius: 0;
        height: 100px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;

        @include bp(laptop) {
            height: 170px;
        }
        
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    .placeholder-svg {
        border-bottom: 1px solid rgba(200, 201, 207, 0.5);
    }

    .card__content {
        justify-self: stretch;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: gap(2);
    }

    .card__resource-footer {
        margin-top: gap(1);

        @include bp(laptop) {
            margin-top: gap(1.6);
        }
    }

    .card__link--overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        cursor: pointer;
    }

    @include bp(laptop) {
        &:hover {
            box-shadow: $card-box-shadow;
            border: 1px solid $border-color;
            transition: all 0.25s ease-in-out;

            .card__resource-wrapper{
                border: 1px solid $border-color;
                transition: all 0.25s ease-in-out;
            }

            .card__title {
                color: $primary-color;
            }

            .button {
                background-color: transparent;
                color: $primary-color;
                text-decoration: underline;
                text-decoration-color: $secondary-color;

                .icon {
                    color: $secondary-color;
                }
            }
        }
    }
}


// -------------------------------------
// Card -- Employee
// -------------------------------------
.card--employee {

    .card__copy {
        margin-bottom: gap(1);
    }

    .card__image-wrapper {
        position: relative;
        height: auto;
        border-radius: 0;
        @include aspect-ratio(1,1);

        .background-image {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}

// -------------------------------------
// Card -- Partner
// -------------------------------------
.card--partner {
    position: relative;
    display: flex;
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid transparent;
    transition: all 0.25s ease-in-out;
    margin-bottom: gap(2.3);
    text-align: left;

    @include bp(laptop) {
        margin-bottom: 0;
    }

    .card__partner-wrapper {
        display: flex;
        flex-direction: column;
        border: 1px solid #C8C9CF;
        transition: all 0.25s ease-in-out;
        border-radius: 10px;
        width: 100%;
    }

    .card__title {
        @include font-primary;
        @include responsive-fontsize(14, 15, 16, 24);
        color: $primary-color;
        margin: 0;
        font-weight: 800;
    }

    .card__image-wrapper {
        border-radius: 0;
        height: 100px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        border-bottom: 1px solid #C8C9CF;

        @include bp(laptop) {
            height: 170px;
        }
        
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

        &.has-logo-image {
            position: relative;
            overflow: hidden;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            .partner-logo__image {
                opacity: 0;
                object-fit: contain;
                height: 0;
                transition: opacity 0.25s ease-in-out;
            }

            .product__image {
                opacity: 1;
                height: 100%;
                transition: opacity 0.25s ease-in-out;
            }
        }
    }

    .placeholder-svg {
        border-bottom: 1px solid rgba(200, 201, 207, 0.5);
    }

    .card__content {
        justify-self: stretch;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: gap(2);
    }

    .card__partner-footer {
        margin-top: gap(1);

        .button--text {
            margin-top: 0;
        }

        @include bp(laptop) {
            margin-top: gap(1.6);
        }
    }

    .card__link--overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        cursor: pointer;
    }

    &:hover {
        .card__image-wrapper {
            &.has-logo-image {
                .partner-logo__image {
                    opacity: 1;
                    padding: 15px;
                    height: 100%;
    
                    @include bp(laptop) {
                        padding: 30px;
                    }
                }
    
                .product__image {
                    opacity: 0;
                    height: 0;
                }
            }
        }
    }

    @include bp(laptop) {
        &.has-link {
            &:hover {
                box-shadow: $card-box-shadow;
                border: 1px solid $border-color;
                transition: all 0.25s ease-in-out;
    
                .card__partner-wrapper{
                    border: 1px solid $border-color;
                    transition: all 0.25s ease-in-out;
                }
    
                .card__title {
                    color: $primary-color;
                }
    
                .button {
                    background-color: transparent;
                    color: $primary-color;
                    text-decoration: underline;
                    text-decoration-color: $secondary-color;
    
                    .icon {
                        color: $secondary-color;
                    }
                }
            }
        }
    }
}