// ----------------------------------------------------------------------------
// Button
// ----------------------------------------------------------------------------
//
// Default button styles to be included in various components. Contains styles
// for the default button effect (fill) only; to use different styles, include
// the associated variant's SCSS file in your project.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Default Variables
// -------------------------------------

$button-color-palette: $color-palette !default;
$button-border-radius: 25px;
$button-transition: $anim !default;


// -------------------------------------
// Default Button Styles
// -------------------------------------

button,
.button {
    // Shopify Defaults
    appearance: none;
    border: 0;
    cursor: pointer;
    display: inline-block;
    line-height: 1;
    text-decoration: none;
    user-select: none;
    white-space: nowrap;

    // iamota Customizations
    border-radius: $button-border-radius;
    padding: 10px 40px;
    text-align: center;
    transition: $button-transition;

    @include font-header;
    @include font-size(16);
    @include line-height(24);

    // Disabled Styles live here because they override everything
    &.button--disabled,
    &:disabled {
        background-color: $white;
        border: 2px solid rgba(200, 201, 207, .5);
        color:  rgba(#C8C9CF, .5);
        pointer-events: none;

        &.button--text {
            background-color: initial;
            opacity: 0.3;

            &:hover,
            a:hover & {
                background-color: initial;
            }
        }

        .btn--add-to-cart__pricing {
            display: none;
        }
    }

    &.button--secondary,
    &.button--disabled--secondary {
        border-radius: 0;
    }
}

// Disabled "link" style
.link.link--disabled {
    color: map-get($button-color--disabled, color);
    cursor: default;
    opacity: 0.3;

    &:hover,
    a:hover & {
        color: map-get($button-color--disabled, color);
    }
}


// -------------------------------------
// Default Colors & Sizes
// -------------------------------------

@each $color-name, $color-set in $button-color-palette {
    .button--#{""+$color-name} {
        background-color: map-get($color-set, color);
        color: map-get($color-set, contrast);
        border: 2px solid map-get($color-set, borderColor);

        &:focus {
            color: map-get($color-set, contrast);
            outline: 2px solid map-get($color-set, outline);
            outline-offset: 0;
        }

        &:hover,
        &:active,
        a:hover & {
            background-color: map-get($color-set, linkHover);
            color: map-get($color-set, colorHover);
        }

        &.button--disabled,
        &:disabled {
            background-color: $white;
            border: 2px solid rgba(200, 201, 207, .5);
            color:  rgba(#C8C9CF, .5);
            pointer-events: none;
        }
    }

    .link--#{""+$color-name} {
        color: map-get($color-set, color);

        &:hover,
        a:hover & {
            color: map-get($color-set, hover-color);
        }
    }
}


// -------------------------------------
// Button Collection
// -------------------------------------

.button-collection {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    list-style: none;
    margin: 0;
    margin-top: $grid-gutter;
    padding: 0;

    &.button-collection--left {
        justify-content: flex-start;
    }

    &.button-collection--center {
        justify-content: center;
    }

    &.button-collection--right {
        justify-content: flex-end;
    }
}

.button-collection--contact {
    margin-top: gap(3.5);
}