// ----------------------------------------------------------------------------
// Banner Block
// ----------------------------------------------------------------------------
//
// A component that allows for text over an image, with optional content and
// link behaviour. A background image overlay is also optionally included.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$banner-block-min-height: 500px;
$banner-block-hover-transition: $anim !default;
$banner-block-overlay-color: rgba($black, .2) !default;
$banner-block-overlay-color-hover: rgba($black, .4) !default;


// -------------------------------------
// Banner Block -- Default
// -------------------------------------
.banner-block {
    width: 100%;

    .background-media {
        padding: gap(1) 0;
        display: block;
        grid-template-columns: [gutter-start] 20px [content] 1fr 20px [gutter-end];
        grid-template-rows: [image-start] minmax(20px, 1fr) [content] auto minmax(20px, 1fr) [image-end];

        &.background-media--with-image {
            display: grid;
            height: $banner-block-min-height;

            @include bpgd(tablet) {
                display: block;
                height: auto;

                .background-media__background {
                    @include aspect-ratio(1, 0.8);
                    position: relative;

                    .background-image {
                        position: absolute;
                    }
                }
            }
        }


        @include bp(laptop) {
            grid-template-columns: [gutter-start] minmax(60px, 1fr) [content-start] minmax(0, #{math.div($max-width, 3)}) [center] minmax(0, #{math.div($max-width, 3)}) [content-end] minmax(0, #{math.div($max-width, 3)}) [content-full] minmax(60px, 1fr) [gutter-end];
            grid-template-rows: [image-start] 0 [content] 0 [image-end];

            &.background-media--with-image {
                grid-template-rows: [image-start] minmax(60px, 1fr) [content] auto minmax(60px, 1fr) [image-end];
            }
        }
    }

    .background-media__content {
        grid-column: content;
        grid-row: content;
        max-width: 550px;

        &.background-media__content--left {
            margin: gap(1.5);
            padding: gap(3);
            border-radius: 10px;
            border: 2px solid $border-color;
            background-color: $white;

            @include bp(laptop) {
                margin: 0;
            }
        }

        &:not(.background-media__content--left) {
            padding: 0 gap(1.5);
            margin: 0 auto;
            text-align: center;

            @include bp(laptop) {
                padding: 0;
            }
        }

        @include bp(laptop) {
            grid-column-start: content-start;
            grid-column-end: content-full;

            &.background-media__content--left {
                grid-column-start: content-start;
                grid-column-end: content-end;
            }
            grid-row: content;
        }
    }
}

.banner-block__link {
    display: block;
    text-decoration: none;
}


.banner-block__title {
    margin: 0;
}

.banner-block__content {
    @include font-default;
}

.banner-block__content + .button {
    margin-top: gap(2);
}



// -------------------------------------
// Banner Block -- Overlay
// -------------------------------------
.banner-block--with-overlay {
    .background-media__overlay {
        transition: $banner-block-hover-transition;
    }

    &:hover {
        .background-media__overlay {
            background: $banner-block-overlay-color-hover;
        }
    }

}


// -------------------------------------
// Banner Block -- Default
// -------------------------------------
.page-section--banner-block {
    .banner-block__title {
        margin-bottom: gap(2);
    }
}


// -------------------------------------
// Banner Block -- Split
// -------------------------------------
.page-section--split-block {
    grid-column: main;

    .banner-block__split-container {
        background-color: $tertiary-color;

        @include bp(tablet) {
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            grid-template-rows: auto;
        }
    }
    .background-media {
        grid-column: 4 / span 2;
        grid-row: 1;
        height: 400px;
        align-self: end;

        .background-media__overlay { z-index: 3;}
        .background-media__background { z-index: 2;}

        .background-image {
            background-color: transparent;
            object-position: top center;
        }
    }

    .background-media__content {
        grid-column: 1 / span 3;
        grid-row: 1;
        justify-items: center;
        align-self: center;

        padding: gap(6) gap(2);

        @include bp(tablet) {
            padding: gap(2) gap(6);
        }
    }

    .banner-block__title {
        margin-bottom: 10px;
    }
}

// -------------------------------------
// Banner Block -- Collection
// -------------------------------------
.page-section--collection-driver {
    grid-column: span 2;

    @include bp(tablet) {
        grid-column: span 1;
    }

    .banner-block {
        height: 100%;

        .banner-block__link {
            height: 100%;
        }

        .background-media__overlay {
            //background: rgba($secondary-color, 0.8);
        }

        .background-media {
            height: 100%;
            grid-template-columns: [gutter-start] 25px [content] 1fr 25px [gutter-end];
            grid-template-rows: [image-start] 25px [content] auto 25px [image-end];
            justify-items: stretch;
        }

        .background-media__content {
            grid-column: content;
            grid-row: content;
            align-self: end;

            @include bp(laptop) {
                @include font-size(25);
                @include line-height(30);
            }
        }

        .banner-block__title {
            margin-bottom: gap(2);
        }

        .button {
            margin-top: gap(2);
            width: 100%;
        }
    }
}
