// ----------------------------------------------------------------------------
// Events List
// ----------------------------------------------------------------------------
//
// This is a section that displays 4 upcoming Events for a specific event blog.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Events List -- Default
// -------------------------------------

.page-section__header--events-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: gap(1);

    @include bp(laptop) {
        margin-bottom: gap(6);
        flex-flow: row nowrap;
    }
}

.events-list__title {
    margin-top: 0;
    margin-bottom: gap(2);

    @include bp(laptop) {
        margin-bottom: 0;
    }
}

.events-list--coming-soon {
    height: 300px;
    background-color: rgb(200, 201, 207, 0.2);
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    border-radius: 10px;

    h4{
        @include responsive-fontsize(24, 32, 24, 32);
        color: $primary-text-color;
    }
}
