// ----------------------------------------------------------------------------
// Bold Upsell
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Default Variables
// -------------------------------------
$button-color-palette: $color-palette !default;
$button-border-radius: 25px;
$button-transition: $anim !default;

.bold-upsell__products-list {
    .bold-grid__column {
        &:not(:only-of-type) {
            .bold-product {
                display: flex;
                flex-direction: column;
            }
        }
    }

    .bold-product-upsell__actions {
        order: 100;
        margin-bottom: 10px;

        .bold-product__button {
            border-radius: $button-border-radius;
            padding: 10px 40px;
            text-align: center;
            transition: $button-transition;
            background-color: transparent;
            color: $primary-color;
            border: 2px solid $primary-color;
            @include font-header;
            @include font-size(16);
            @include line-height(24);

            &:hover,
            &:active,
            a:hover & {
                background-color: $primary-color;
                color: $white;
            }
        }
    }

    .bold-product__details {
        .limit_disclaimer {
            color: $bright-blue;
            opacity: 1;
        }
    }
}