// -------------------------------------------
// Country Switcher
// Dropdown toggle navigation that switches
// between countries sites
// -------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$country-switcher-background: $white;
$country-switcher-border: $border--light;
$country-switcher-link-color: $primary-text-color;


// -------------------------------------
// Country Switcher -- Default
// -------------------------------------
.country-switcher {
    position: relative;
    z-index: 0;

    &.menu__item {
        padding: 0;
        margin-right: 5px;
    }
}

.country-switcher__toggle {
    display: flex;
    align-items: center;

    @include bp(laptop) {
        padding: 10px;
    }

    @include bp(desktop) {
        padding: 10px 20px;
    }

    .menu__title {
        margin-right: gap(.5);
    }
}

.country-switcher__list {
    display: none;
    background-color: $country-switcher-background;
    list-style: none;
    padding: gap(1) 0;
    width: 100%;
    border: $country-switcher-border;

    &.is-open {
        display: block;

        @include bp(laptop) {
            position: absolute;
            padding: gap(1) gap(2);
        }
    }
}

.country-switcher__link {
    @include font-primary;
    @include font-size(16);
    text-decoration: none;
    color: $country-switcher-link-color;

    @include bp(laptop) {
        @include font-size(16);
    }

    .is-current & {
        display: none;
    }
}

.country-toggle__icon {

    .icon {
        transform: rotate(0deg);
        transform-origin: center center;
        transition: $anim;
    }


    .is-open & {
        .icon {
            transform: rotate(-180deg);
        }
    }
}
