// ----------------------------------------------------------------------------
// Accordion
// ----------------------------------------------------------------------------
//
// Collapsible accordion block used to reveal extra product information when the
// user clicks an item's header or expand button.
//
// Related JS file: accordion/accordion.js
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$accordion-transition:                $anim !default;
$accordion-active-max-height:         5000px !default;
$accordion-title-color:               $black !default;
$accordion-item-margin-bottom:        gap(1.5) !default;
$accordion-item-border:               1px solid $grey !default;
$accordion-item-hover-color:          $bright-blue;
$accordion-box-shadow:                0 !default;
$accordion-question-background-color: transparent !default;
$accordion-question-mobile-padding:   gap(1.5) 0 !default;
$accordion-question-padding:          gap(2) 0 !default;
$accordion-answer-background-color:   transparent !default;
$accordion-content-padding:           0 !default;
$accordion-icon-color:                $white !default;
$accordion-active-content-padding:    0 0 gap(2) !default;


// -------------------------------------
// accordion
// -------------------------------------
.accordion {
    width: 100%;
    border-top: $accordion-item-border;
}

.accordion__item {
   &:not(:last-of-type){
        border-bottom: $accordion-item-border;
   }

    &.is-open {
        box-shadow: $accordion-box-shadow;
    }

    @include bp(laptop) {
        &:hover {
            border-color: $accordion-item-hover-color;

            .accordion__button {
                color: $accordion-item-hover-color;
            }

            .accordion__icon--close,
            .accordion__icon--open {
                color: $accordion-item-hover-color;
            }
        }
    }
}

.accordion__title {
    margin: 0;

    & > * {
        color: $black;
    }

    .accordion__button {
        @include font-header;
        font-size: 1em;
        background-color: $accordion-question-background-color;
        box-shadow: $accordion-box-shadow;
        color: $accordion-title-color;
        cursor: pointer;
        overflow: hidden;
        position: relative;
        text-align: left;
        text-transform: none;
        transition: $accordion-transition;
        padding: $accordion-question-mobile-padding;
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        white-space: normal;
    }

    @include bp(laptop) {
        .accordion__button {
            padding: $accordion-question-padding;
        }
    }
}

.accordion__icon {
    display: inline-block;
    pointer-events: none;
    transition: $accordion-transition;
    margin-left: gap(2);
    color: $black;

    .icon {
        width: 16px;
        height: 16px;
    }

    .accordion__icon--close {
        display: none;

        .is-open & {
            display: block;
        }
    }

    .accordion__icon--open {
        display: block;

        .is-open & {
            display: none;
        }
    }
}

.accordion__content {
    @include clearfix;
    background-color: $accordion-answer-background-color;
    max-height: 0;
    display: block;
    overflow: hidden;
    padding: $accordion-content-padding;
    text-align: left;
    transition: $accordion-transition;

    &.is-open {
        max-height: $accordion-active-max-height;
        padding: $accordion-active-content-padding;
        transition: $accordion-transition;
    }

    p:last-child {
        margin-bottom: 0;
    }
}
