// ----------------------------------------------------------------------------
// Article
// ----------------------------------------------------------------------------
//
// A page template which displays blog article content.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


.page-section--article {
    .container {
        width: 100%;

        @include bp(tablet) {
            @include container--wysiwyg;
        }
    }

    .article__feature-image {
        overflow: hidden;
        line-height: 0;

        img {
            object-fit: cover;
            object-position: center center;
            height: 100%;
            width: 100%;
        }

        .ie11 & {
            height: auto;

            img {
                height: auto;
            }
        }
    }
}


// ----------------------------------------------------------------------------
// Article - Employee
// ----------------------------------------------------------------------------
.article--employee {

    .article__feature-image {
        height: auto;
        @include aspect-ratio(1, 1);
    }
}

// ----------------------------------------------------------------------------
// Article - Event
// ----------------------------------------------------------------------------
.article--event {
    max-width: 1198px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;

    @include bp(tablet) {
        margin-top: gap(-2.5);
    }

    .article__summary-container {
        order: 2;

        @include bp(tablet) {
            order: 1;
        }
    }

    .article__feature-image-container {
        order: 1;

        @include bp(tablet) {
            order: 2;
        }
    }

    .artilce__header {
        padding: gap(2);
        background-color: rgba($light-grey, .2);
        border-radius: 20px;
        margin-bottom: gap(2);

        .css-grid {

            @include bp(laptop) {
                grid-gap: gap(5.5);
            }
        }

        .card__link {
            margin-top: gap(2);
        }

        @include bp(laptop) {
            padding: gap(6);
        }
    }

    .article__event-type {
        @include font-size(14);
        font-weight: 700;
        color: $black;
        text-transform: uppercase;
    }

    .event-type__icon {
        @include font-size(22);
        position: relative;
        top: 5px;
        margin-right: 5px;
    }

    .page-header__title {
        margin-top: gap(1);
        color: $black;
    }

    .article__feature-image {
        border-radius: 10px;
        box-shadow: 0 5px 20px rgba(0, 0, 0, .1);
    }

    .article__event-date {
        margin-bottom: gap(2);
        color: $black;
    }

    .article__event-credit,
    .article__event-price {
        margin: 0;
        color: $black;
    }

    .blog-article__content {
        padding: gap(2);
        background-color: rgba($light-grey, .2);
        border-radius: 20px;

        @include bp(laptop) {
            padding: gap(6);
        }

        .event-details__heading {
            color: $black;
            margin-top: 0;
            margin-bottom: gap(2);
        }

        .card__link--event-details {
            margin-top: gap(3);
        }
    }
}
