// ----------------------------------------------------------------------------
// Newsletter
// ----------------------------------------------------------------------------
//
// The newletter component is a custom form that allows a user to sign up for
// a mailing list. This file provides base styles for the newsletter snippet
// (the form partial only) as well as section-specific styles for the full
// newsletter section.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$newsletter-height: 45px !default;
$newsletter-button-width: 60px !default;

// Newsletter section
$newsletter-section-background: $background-color;
$newsletter-section-text-color: $primary-text-color;

// Newsletter footer
$newsletter-footer-text-color: $white;


// -------------------------------------
// Newsletter Layout
// This configures the side-by-side
// layout for the newsletter
// -------------------------------------
.newsletter--default {
    display: block;

    .form--newsletter {
        @include clearfix;
        max-width: 400px;
    }

    .newsletter__wrapper {
        overflow: auto;
    }

    .newsletter__input,
    .newsletter__submit {
        height: $newsletter-height;
        padding: 12px 0px 11px;
    }

    .newsletter__input {
        background-color: transparent;
        border: 0;
        color: $primary-text-color;
        padding-right: 20px;

        &::placeholder {
            color: $caption-text-color;
        }

        &:-moz-placeholder {
            color: $caption-text-color;
        }

        &:-ms-input-placeholder {
            color: $caption-text-color;
        }

        &::-ms-input-placeholder {
            color: $caption-text-color;
        }

        &::-webkit-input-placeholder {
            color: $caption-text-color;
        }
    }

    // Newsletter Input Wrapper
    .form__field--email {
        margin-bottom: 10px;
        float: left;
        width: calc(100% - #{$newsletter-button-width});

        .form__label {
            display: none;
        }
    }

    // Newsletter Success Message
    .newsletter__success {
        color: $white;
    }

    // Newsletter Submit Button Wrapper
    .form__field--button {
        margin-bottom: 10px;
        float: left;
        width: $newsletter-button-width;


        .newsletter__submit {
            @include font-size(26);
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;

            &:focus {
                outline-offset: -6px;
            }
        }
    }
}



// -------------------------------------
// Newsletter Section
// -------------------------------------
.newsletter-section {
    background-color: $newsletter-section-background;
    padding: $grid-gutter $grid-gutter--mobile;

    @include bp(tablet) {
        text-align: center;
    }

    @include bp(laptop) {
        padding: ($grid-gutter * 2) $grid-gutter;
    }

    .newsletter__title {
        color: $newsletter-section-text-color;
        margin: 0 0 gap(1);
    }

    .newsletter__content {
        @include bp(tablet) {
            @include container--wysiwyg;
        }
    }

    .form--newsletter {
        margin: $grid-gutter 0;

        @include bp(tablet) {
            margin: $grid-gutter auto;
        }

        @include bp(laptop) {
            margin-bottom: 0;
        }
    }

    .newsletter__wrapper {
        border-bottom: $border--light;
    }

    .newsletter__success {
        color: $newsletter-section-text-color;
    }

    .newsletter__input,
    .newsletter__submit {
        background-color: transparent;
        border: 0;
        border-radius: 0;
        color: $newsletter-section-text-color;
    }

    .newsletter__input {

        &:focus {
            border: 1px solid $primary-color;
            outline: auto;
        }
    }
}



// -------------------------------------
// Newsletter Footer
// -------------------------------------
.footer {
    .form--newsletter {
        margin-bottom: gap(6);

        @include bp(laptop) {
            margin-bottom: gap(2);
        }

        .errors {

            ul {
                padding-left: 0;
                
                li {
                    list-style-type: none;
                }
            }

        }

        .form__error,
        label.error,
        .errors ul li {
            @include font-default;
            font-weight: bold;
            color: $black;
            background-color: $error-color;
            padding: 5px 10px;
        }
    }

    .newsletter__wrapper {
        border-bottom: 1px solid $newsletter-footer-text-color;
        margin-bottom: 20px;
    }

    .newsletter__input {
        color: $newsletter-footer-text-color;

        &:focus {
            border: 1px solid $white;
            outline: auto;
        }

        &::placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &:-moz-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &:-ms-input-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &::-ms-input-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &::-webkit-input-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }
    }

    .newsletter__submit {
        color: $newsletter-footer-text-color;

        &:hover {
            color: $newsletter-footer-text-color;
        }
    }
}
