// ----------------------------------------------------------------------------
// Product Menu
// ----------------------------------------------------------------------------
//
// This is a section that displays product items
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$carousel-arrow-background: $white;
$carousel-arrow-border-radius: 5px !default;
$carousel-arrow-color: $primary-color;
$carousel-arrow-box-shadow: 0px 4.008px 20.04px 2.004px rgba(0, 0, 0, 0.05);;
$carousel-arrow-padding: 0;
$carousel-arrow-padding--desktop: calc((#{$grid-gutter--mobile} * 2));
$carousel-container-background-color--blue: rgba(45, 74, 249, 0.2);
$carousel-container-background-color--coral: rgba(242, 88, 81, 0.5);
$carousel-container-max-width: 1440px;


// -------------------------------------
// Product List - Section
// -------------------------------------
.product-slider {
    .subtitle {
        display: block;
        margin-bottom: gap(1);
    }
}

.product-slider__title {
    margin: 0;
}

// -------------------------------------
// Product List - Carousel
// -------------------------------------
.carousel--product-slider-wrapper{
    position: relative;
    max-width: $carousel-container-max-width;
    margin: 0 auto;

    .swiper-arrow {
        background: $carousel-arrow-background;
        border: 1px solid $border-color--light;
        border-radius: $carousel-arrow-border-radius;
        box-shadow: $carousel-arrow-box-shadow;
        color: $carousel-arrow-color;
        padding: $carousel-arrow-padding;
        height: 51px;
        width: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        top: 35%;

        &:hover {
            border-color: $cyan;
        }

        &.swiper-button-next {
            right: $carousel-arrow-padding;

            @include bp(tablet) {
                right: $grid-gutter--mobile;
            }

            @include bp(laptop) {
                right: $carousel-arrow-padding--desktop;
            }

            &::after {
                content: "";
            }
        }

        &.swiper-button-prev {
            border-radius: $carousel-arrow-border-radius;
            left: $carousel-arrow-padding;

            @include bp(tablet) {
                left: $grid-gutter--mobile;
            }

            @include bp(laptop) {
                left: $carousel-arrow-padding--desktop;
            }

            &::after {
                content: "";
            }

            .icon {
                margin-right: 1px;
            }
        }

        &.swiper-button-disabled {
            display: none;
        }

        svg {
            height: 15px;
            width: 8px;
        }
    }
}

.carousel--product-slider {
    margin-top: gap(1.4);

    @include bp(laptop) {
        margin-top: gap(1.6);
    }

    .swiper-wrapper {
        position: relative;
        padding: 0;
        list-style-type: none;

        @include bp(laptop) {
            margin: gap(2.5) 0;
            margin-bottom: 0;
        }
    }


    .swiper-pagination.swiper-pagination-bullets {
        position: relative;
        margin-top: gap(4);
        margin-bottom: 0;

        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            background-color: $grey;
            opacity: 1;

            &:not(:last-child) {
                margin-right: gap(.3);
            }
        }

        .swiper-pagination-bullet-active {
            background-color: $secondary-color;
        }
    }

    .slider__icon {
        svg {
            width: 40px;
            height: 40px;
        }
    }
}


