// ----------------------------------------------------------------------------
// Mini Cart
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$mini-cart-dropdown-background-color: $white !default;
$mini-cart-dropdown-min-width: 350px !default;
$mini-cart-dropdown-max-height: 400px !default;
// Transition Variables: property name | duration | timing function | delay
$mini-cart-dropdown-transition: $anim, visibility 0s linear .01s, z-index 0s linear 0s !default;



// ------------------------------
// mini-cart -- Dropdown
// ------------------------------
.mini-cart--dropdown {
    @include bp(laptop) {
        .mini-cart__list {
            align-items: flex-end;
        }
    }
}

.mini-cart__icon-label {
    display: none;

    @include bp(tablet) {
        display: inline-block;
    }
}

.mini-cart__item--dropdown {
    position: relative;
    transition: $anim;


    .mini-cart__header-link {
        @include font-default;
        @include font-size(16);
        color: $primary-color;
        font-weight: 700;
        text-decoration: none;
    }

    .icon {
        transition: $anim;
        top: 2px;
        position: relative;
    }

    // Exclude dropdown hover to large screens
    @include bp(laptop) {
        &.js--isOpen,
        &:hover {
            .dropdown--mini-cart {
                opacity: 1;
                transform: translateY(0%);
                visibility: visible;
                z-index: 1000;
            }
        }
    }
}


// ------------------------------
// Dropdown Styles
// ------------------------------
.dropdown--mini-cart {
    display: none;

    @include bp(laptop) {
        background-color: $mini-cart-dropdown-background-color;
        display: block;
        min-width: $mini-cart-dropdown-min-width;
        position: absolute;
        top: 100%;
        right: 0;
        border: $border--light;

        // Transition animation
        opacity: 0;
        transform: translateY(-3em);
        transition: $mini-cart-dropdown-transition;
        visibility: hidden;
        z-index: -1;
    }

    .template-cart & {
        display: none;
    }
}


// ------------------------------
// Mini Cart items
// ------------------------------
.mini-cart__items-container {
    max-height: $mini-cart-dropdown-max-height;
    overflow-y: scroll;
    padding: gap(2);
}

.mini-cart__items-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.mini-cart__line-item {
    padding: gap(1.5) 0;

    & + .mini-cart__line-item {
        border-top: $border--light;
    }

    .card--line-item {
        grid-template-columns: 80px auto;

        .ie11 & .line-item__image {
            max-width: 80px;
        }
    }

    .card__title {
        @include font-size(16);
        margin-bottom: gap(.5);
    }
}

// ------------------------------
// Mini Cart Footer
// ------------------------------
.mini-cart__footer {
    padding: 20px;
    border-top: $border--light;

    .button-collection {
        margin-bottom: 0;
        justify-content: flex-end;

        .button + .button {
            margin-left: gap(1.5);
        }
    }

    .button__checkout,
    .button__edit-cart {
        width: calc(50% - 8px);
        padding: 14px 24px 13px;
    }

    .button__edit-cart {

        &:focus {
            color: $primary-color;
        }

        &:hover:focus {
            color: $white;
        }
    }

    .additional-checkout-buttons {
        margin-top: gap(3);
    }
}
