// ----------------------------------------------------------------------------
// Hero Banner
// ----------------------------------------------------------------------------
//
// Jumbo banner typically used for the Homepage Hero. Showcases large image
// and title along a description and CTA.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$hero-banner-height:          375px;
$hero-banner-height--desktop: 650px;


// -------------------------------------
// Hero Banner -- Default
// -------------------------------------

.hero-banner {
    margin-bottom: $grid-gutter--mobile;
    overflow: hidden;

    @include bp(laptop) {
        margin-bottom: $grid-gutter;
    }

    .background-media__content {
        text-align: center;
    }

    .hero-banner__title {

        &:not(.hero-banner__title--split) {
            @include font-jumbo;
        }
        margin: 0;
        margin-bottom: 20px;
        color: $white;

        @include bp(laptop) {
            margin-bottom: 30px;
        }
    }
}


// -------------------------------------
// Hero Banner - Media (Image & Video)
// -------------------------------------
.hero-banner--image {

    .background-media--image,
    .background-media--video {
        height: auto;
        display: block;

        .background-media__background {
            @include aspect-ratio(1, 0.8);
            position: relative;

            .background-image {
                position: absolute;
            }

            @include bp(tablet) {
                @include aspect-ratio(1, 0.3);
            }
        }

        @include bp(laptop) {
            display: grid;
            height: $hero-banner-height--desktop;
            grid-template-columns: [gutter-start] minmax(60px, 1fr) [content-start] minmax(0, #{math.div($max-width, 3)}) [center] minmax(0, #{math.div($max-width, 3)}) [content-end] minmax(0, #{math.div($max-width, 3)}) [content-full] minmax(60px, 1fr) [gutter-end];
        }
    }

    .background-media__content {
        padding: gap(1.5);
        text-align: left;
        grid-column: content;
        grid-row: content;
        border-radius: 20px;

        @include bp(laptop) {
            max-width: 800px;
            padding: 0;
        }
    }

    .hero-banner__title {
        color: $black;

        &.hero-banner__title--outline {
            @supports((text-stroke: 1px black) or (-webkit-text-stroke: 1px black)) {
                text-stroke: 1px $black;
                -webkit-text-stroke: 1px $black;
                color: transparent;
            }
        }
    }

    .hero-banner__text {
        width: 100%;
        margin: 0;
        text-align: left;
        color: $primary-text-color;
    }

    .button--hero-banner {
        margin-top: gap(2);
    }

    &.hero-banner--white {
        .background-media__content {
            background-color: $white;

            @include bp(laptop) {
                padding: gap(4);
            }
        }
    }

    .background-media--video {


        .background-media__background {
            pointer-events: none;
            z-index: 1;
            
        .background-media--image-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
        }

            &.is-playing {

                @include bp(laptop) {

                    .video-block {
                        z-index: 1;
                    }
                }
            }
        }

        @include bp(laptop) {
            grid-template-rows: [image-start] minmax(60px,1fr) [content] auto minmax(60px,1fr) [image-end];
            overflow: hidden;
        }

        .video-block {
            @include aspect-ratio(16, 9);
            background: $background-color--light;
            background-position: 50% 50%;
            background-size: cover;
            width: 100%;
            z-index: -1;
            height: auto;

            @include bp(laptop) {
                height: 110%;
            }

            @include bp(desktop) {
                height: 125%;
            }
        }

        .video-block__video {
             height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
            object-fit: cover;
            object-position: center center;
            vertical-align: bottom;

            iframe {
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
            }
        }

        @media (min-aspect-ratio: 16/9) {
            .video-block__video { height: 300%; top: -100%; }
        }

        @media (max-aspect-ratio: 16/9) {
            .video-block__video { width:300%; left: -100%; }
        }
    }
}


// -------------------------------------
// Hero Banner -- Split
// -------------------------------------

.hero-banner--split {
    background-color: $background-color;
    height: auto;
    display: grid;
    grid-template-columns: [gutter-start] 20px [content] 1fr 20px [gutter-end];
    grid-template-rows: auto auto;

    @include bp(tablet) {
        height: $hero-banner-height;
        grid-template-columns: [gutter-start] minmax(#{$grid-gutter}, 1fr) [content-start] minmax(0, #{$max-width*0.5}) [center] auto [content-end] minmax(0, #{$max-width*0.5}) minmax(#{$grid-gutter}, 1fr) [gutter-end];
        grid-template-rows: auto;
    }

    @include bp(laptop) {
        height: $hero-banner-height--desktop;
    }

    .hero-banner__content {
        color: $primary-text-color;
        text-align: center;
        grid-column: content;
        grid-row: 1;
        margin: gap(4) 0;

        @include bp(tablet) {
            grid-column: 2;
            grid-row: 1;
            align-self: center;
            margin: 0;
            text-align: left;
        }
    }

    .hero-banner__media {
        grid-column: gutter-start / gutter-end;
        grid-row: 2;
        height: 100%;

        @include bp(tablet) {
            grid-column: 4 / span 2;
            grid-row: 1;
        }

        @include bp(laptop) {
             height: $hero-banner-height--desktop;
        }
    }


    .hero-banner__title {
        margin-bottom: 10px;
        color: $primary-text-color;

        @include bp(laptop) {
            margin-bottom: 20px;
        }
    }

    .hero-banner__button {
        margin-top: 20px;
    }
}
