// ----------------------------------------------------------------------------
// Typography
// ----------------------------------------------------------------------------
//
// Contains all site-wide typography styles. All base level HTML typographical
// elements are styled here first, with mixins and variables to help with
// granular includes at the component level.
//
// ----------------------------------------------------------------------------

@font-face {
    font-family:'GilroyExtraBold';
    src: url('../../fonts/GilroyExtraBold.woff2') format('woff2'), /* Super Modern Browsers */
         url('../../fonts/GilroyExtraBold.woff') format('woff'); /* Pretty Modern Browsers */
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-display: swap;
}

// -------------------------------------
// Font Family Variables
// -------------------------------------
$font--primary: 'Urbanist', Helvetica, Arial, sans-serif;
$font--header: 'GilroyExtraBold', Helvetica, Arial, serif;


// -------------------------------------
// Typography Mixins
// -------------------------------------
@mixin responsive-fontsize($m-fontsize, $m-lineheight, $fontsize, $lineheight) {
    @include font-size($m-fontsize);
    @include line-height($m-lineheight);

    @include bp(laptop) {
      @include font-size($fontsize);
      @include line-height($lineheight);
    }
}

// Default font mixin
// This makes no assumption about sizes
@mixin font-default {
    font-family: $font--primary;
    font-style: normal;
    font-weight: 400;
    line-height: 1.625;
}

// Header font mixin
// This makes no assumption about sizes
@mixin font-header {
    font-family: $font--header;
    font-style: normal;
    font-weight: 800;
    line-height: 1.2;
}

@mixin font-header--light {
    @include font-header;
    font-weight: 400;
}

// Default bold font
@mixin font-medium {
    font-family: $font--primary;
    font-style: normal;
    font-weight: 700;
}

// Paragraphs/Default text mixin
// This is used for main element styles; includes sizes and colour
@mixin font-primary {
    @include font-default;
    @include font-size(16);
    @include line-height(24);
    color: $primary-text-color;
}

// Caption text mixin
// This is used for caption text styles; includes sizes and colour
@mixin font-caption {
    @include font-default;
    @include font-size(14);
    @include line-height(20);
    font-weight: 400;
    color: $caption-text-color;
}

// Font size for tags
@mixin font-tags {
    @include font-header;
    @include font-size(14);
    @include line-height(20);
    letter-spacing: 1px;
    color: $secondary-text-color;
}

// Font Navigation Menu
@mixin font-navigation {
    @include font-header;
    @include font-size(18);
    @include line-height(24);
    font-weight: 400;

    @include bp(laptop) {
        @include font-size(16);
    }

    @include bp(desktop) {
        @include font-size(18);
    }
}

// Jumbo Font text mixin
// This is used for homepage hero element styles; includes sizes
@mixin font-jumbo {
    @include font-header;
    @include responsive-fontsize(50, 50, 80, 80);
}
