// ----------------------------------------------------------------------------
// Logo Slider
// ----------------------------------------------------------------------------
//


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$carousel-arrow-background: $white;
$carousel-arrow-border-radius: 5px !default;
$carousel-arrow-color: $primary-color;
$carousel-arrow-box-shadow: 0px 4.008px 20.04px 2.004px rgba(0, 0, 0, 0.05);;
$carousel-arrow-padding: 0;
$carousel-arrow-padding--desktop: calc((#{$grid-gutter--mobile} * 2));
$carousel-container-background-color--blue: rgba(45, 74, 249, 0.2);
$carousel-container-background-color--coral: rgba(242, 88, 81, 0.5);
$carousel-container-max-width: calc($container--desktop + (#{$grid-gutter} * 2) - (#{$carousel-arrow-padding--desktop} * 2));


// -------------------------------------
// Logo Slider - Section
// -------------------------------------
.page-section--logo-slider {

    .container {
        width: 100%;
        max-width: $carousel-container-max-width;

        // @include bpgd(laptop) {
        //     width: 100%;
        //     max-width: $carousel-container-max-width;
        // }
    }
}

.page-section__header--logo-slider {
    margin-left: $grid-gutter--mobile;
    margin-right: $grid-gutter--mobile;
    margin-bottom: gap(3);
    text-align: left;
    max-width: 1064px;

    @include bp(laptop) {
        text-align: center;
        margin-right: auto;
        margin-left: auto;
    }

    .page-section__title {
        margin-top: 0;
        margin-bottom: gap(2);
    }

    .page-section__content {
        margin-bottom: gap(3);
    }
}



// -------------------------------------
// Logo Slider - Carousel
// -------------------------------------
.carousel--logo-slider {
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;

        @include bp(laptop) {
            width: calc(75% - 50px);
            left: 60px;
        }
    }

    &.swiper{
        @include bp(tablet) {
            margin: 0 $grid-gutter--mobile;
            padding: 0 gap(1.5);
        }

        @include bp(laptop) {
            padding: 0 gap(2.5);
        }

        @media (min-width: 1440px){
            margin: 0;
        }
    }


    .swiper-wrapper {
        position: relative;
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    .swiper-slide {
        margin: auto 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;

        img{
            width: 50%;
            padding: 0 gap(3);
            height: auto;
            object-fit: contain;

            @include bp(tablet) {
                width: 25%;
            }

            @include bp(laptop) {
                padding: 0 gap(4.5);
            }
        }
    }

    .swiper-arrow {
        background: $carousel-arrow-background;
        border: 1px solid $border-color--light;
        border-radius: $carousel-arrow-border-radius;
        box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
        color: $carousel-arrow-color;
        padding: $carousel-arrow-padding;
        height: 51px;
        width: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        top: calc(50% - gap(3));

        &:hover {
            box-shadow: $carousel-arrow-box-shadow;
            border-color: $cyan;
        }

        &.swiper-button-next {
            right: $carousel-arrow-padding;

            @include bp(laptop) {
                right: $grid-gutter--mobile;
            }

            @media (min-width: 1440px){
                right: $carousel-arrow-padding;
            }

            &::after {
                content: "";
            }
        }

        &.swiper-button-prev {
            border-radius: $carousel-arrow-border-radius;
            left: $carousel-arrow-padding;

            @include bp(tablet) {
                left: $grid-gutter--mobile;
            }

            @media (min-width: 1440px){
                left: $carousel-arrow-padding;
            }

            &::after {
                content: "";
            }


            .icon {
                position: relative;
                left: 1px;
            }
        }

        &.swiper-button-disabled {
            display: none;
        }

        svg {
            height: 15px;
            width: 8px;
        }
    }

    .swiper-pagination.swiper-pagination-bullets {
        position: relative;
        margin-top: gap(4);
        margin-bottom: 0;

        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            background-color: $grey;
            opacity: 1;

            &:not(:last-child) {
                margin-right: gap(.3);
            }
        }

        .swiper-pagination-bullet-active {
            background-color: $secondary-color;
        }
    }

    .slider__icon {

        svg {
            width: 40px;
            height: 40px;
        }
    }
}
